      "We are located on a rural one lane road going from sea level-1000ft.  It's a nice quiet stroll up and down Kama'ili Rd. It starts off Highway 130 and ends at Opihikao on the red road, another amazing one lane road along our east coast with the cleanest air in the world.<br />There are no street lights in this area, so you are guided by the stars and moon light.  With very little noise on the farm you'll rarely hear cars, neighbors, sirens, etc.  You will hear leaves falling,  chickens squawking during the day, and maybe your lovers heartbeat.",
