      "UPDATE: We had our windows and slider replaced so our walls currently will not have matching paint applied. Our condo is located right across the street from the Pacific Ocean. In a quiet neighborhood outside of downtown Kailua-Kona on Alii Drive our second home is a humble getaway in paradise!<br /><br /><b>The space</b><br />Stay at the ROYAL KAHILI CONDOMINIUMS with a garden and partial ocean view and experience Hawaii kama'aina style! Just 2.5 miles outside of the heart of downtown Kona on Alii Drive, this is a wonderful hideaway for your Hawaiian vacation. Close enough to the action and the beaches but still get to enjoy a quiet neighborhood to relax in when you're not out and about. <br /><br />With a fully furnished 2bd/2ba condo, all you need is your toothbrush! <br /><br />Our condo features a full kitchen, living room & dining area, private lanai, air conditioning, ceiling fans, HDTV flat screen satellite tv's in both the living room and master bedroom, full washer & dryer in",
    neighborhood_overview:
      "Close to the heart of downtown Kona, but far enough away to enjoy the quiet during evenings. Located across the street from the ocean, you're in a residential area close to shops, beaches and the main highway around the island.",
