    host_name: 'Noah',
    host_since: '2009-06-13',
    host_location: 'Seattle, WA',
    host_about:
      "Our goal as hosts is to provide a comfortable, clean and affordable place to stay in one of our favorite places in the world, the Big Island. The condo is family owned in the heart of Kailua-Kona and we are on call 24/7 during a guest's stay because we understand that not everything happens during business hours and when you're on vacation, you don't want any stress or issues. We offer recommendations on things to do based on the guest's interests as well as fun things we've discovered throughout the years of visiting the island. We want nothing but a wonderful stay for all our guests because that's what we get to experience while booking through airbnb.\r\n\r\nFavorite places we've been: Hawaii (the Big Island), Disneyland & World, Toronto, Seattle, San Francisco, Lake Tahoe, San Juan Islands.\r\nPlaces we'd like to go: Japan, Italy, France, Denmark, Germany, Moscow, Vancouver, New York City, Colorado Rockies, Turkey, India, Morocco, the Dead Sea, Australia, the Islands of the South Pacific and many more!",
