      "The Volcano Forest Escape is for people who want more out of life than the busy side of the island. It's within a 5 minute drive to the Volcanoes National Park, and 40 minutes to the famous black sand beach. House is full of beds, upgrades & charm.<br /><br /><b>The space</b><br />Spread out and enjoy this 3 BR/2BA house on a 1/2 acre. This is a great way to beat the heat and enjoy summertime in Hawaii or come during winter to see snow at the tops of Mauna Loa and/or Mauna Kea. This house is at a 3,700 foot elevation, with cool temperatures and in the middle of a native Hawaiian forest full of blossoming Ohia trees and giant fern trees. Just a mile from Volcanoes National Park, you'll find this park to be one of the most unique national parks with many displays, hiking trails, lava tubes, steam & sulfur vents. You can see how fresh lava covers the land and the process of nature and plants taking it back. There's a multitude of things to do within a 30-45 minute drive of this house - se",
