    source: 'city scrape',
    name: 'EndlessSummer: 300 steps 2 beach!',
    description:
      "Endless Summer Maui, Unit 219, is hosted by an Experienced SUPER HOST that's been with Airbnb since 2009 and has earned SUPERHOST status over 30 consecutive times! Tracy has a proven track record of thousands of 5-star reviews.<br /><br />~ Tracy owns Tracy's Tropical Treasures, named the #1 vacation rentals on Maui twice and #1 in Kihei in 2021!  <br /><br />~ If your dates aren't available or if you’re traveling with a group, contact Tracy on Airbnb to see if it's possible to book 1 of 15 Maui Airbnbs she manages<br /><br /><b>The space</b><br />Aloha from Maui<br /><br />~ Aloha and Welcome to Endless Summer Maui, Unit 219!  <br /><br />~ We are the proud owners of Endless Summer Maui.  As frequent visitors to Maui, we understand what vacationers deserve.  Endless Summer Maui is totally loaded with everything you will need to enjoy your Maui vacation at the best possible price.  This is a 5-Star Quality, 2nd floor condo that's comparable to any Wailea resort hotel room, only it offe",
    neighborhood_overview:
      'Maui has been voted the "Best Island in the World" for over 23 years.  With daytime temperatures ranging from roughly 85 degrees F during the winter, it\'s easy to see why Maui is such a popular destination choice and the #1 island in the world.<br /><br />Here’s why I think the South side of the island (where Kihei is located) is the best place to stay.  South Maui really has it all!  ;)<br /><br />The sunny South shores of Kihei offer 6 miles of popular, sun kissed beaches with warm, balmy days and cool, tropical nights.  <br /><br />Typically, humidity levels in Kihei are VERY LOW and the annual rainfall in Kihei is LESS than anywhere else on island.  :)<br /><br />Kihei is a sizable beach town with plenty of places to eat and fun things to do.  Kihei is definitely the hub for Maui surf lessons and is considered the true pivoting point for all of Maui’s popular outdoor activities.  <br /><br />Most of the accommodation options in Kihei will be condos with full kitchens (enabling you ',
    picture_url: 'https://a0.muscache.com/pictures/3bc1dde2-a0a8-417e-aa1d-c3ed81c7d829.jpg',
    host_id: '7532',
    host_url: 'https://www.airbnb.com/users/show/7532',
    host_name: 'Tracy',
    host_since: '2009-02-07',
    host_location: 'Hawaii, United States',
    host_about:
      "Aloha from Maui!\n\nCongratulations on your decision to book a vacation in sunny Maui, Hawaii; the #1 island in the world for over 25 years.  :)\n\nI would be honored to help you choose accommodations in Maui that fit your needs, desires, and budget. \n\nAs a full-time resident on Maui for over 15 years, I am very familiar with all that this wonderful Island has to offer.  I absolutely love my job as both an owner and property manager of vacation rentals on Maui.  The fact that I get to help others plan their Hawaiian vacation and/or relocation to the magical island of Maui is literally the icing on top of the cupcake!\n\nIf you are wanting to purchase property on Maui, I am a licensed realtor in the State of Hawai'i, own a property management company in Maui, and am also a certified Home Staging Expert.  I would love to speak with you and answer any questions you might have about vacation rentals on Maui.\n\nIn addition to managing lots of amazing properties directly across the street from the awesome, sun kissed beaches of south Maui, I am also the proud owner of my own vacation rentals.   The condos I own are called Tracy’s Tropical Treasures.  They are listed on Airbnb as Tracy's Tropical Treasure #1 (Ocean View), Tracy's Tropical Treasure #2 (Corner Penthouse), Tracy's Tropical Treasure #3 (Ocean View), and my newest addition, Paradise Awaits You! Tracy's Tropical Treasure #4 (Ocean View, Central A.C., and double pane windows, so no road noise inside the condo).  *Tracy’s Tropical Treasure #3 and Paradise Awaits You! Tracy's Tropical Treasure #4 both house up to 4 guests.  :)\n\nTracy’s Tropical Treasures were Named the #1 Vacation Rentals in all of Maui in 2016.\n\nIn November, 2019, I was RANKED #1 again.  ;)\n\nIn July of 2022, I was ranked #1 in Kihei for the fourth time.\n\nI have worked very hard for these achievements and on every single occasion, it was one of the best days of my life when I received confirmation that I had won the award.  \n\nI look forward to speaking with you, helping you with your Maui vacation/relocation, and sharing my ALOHA spirit.  I have been in the hospitality industry for over 25 years, and it's really how I choose to live my life.  Pampering guests is my #1 priority!  \n\n* With well over 1000 bookings on the 4 rentals I personally own, I am extremely proud and take great pride in the fact that I have so many wonderful reviews.  Please feel free to check out all the reviews on Airbnb for the proof.   Customer Service is very important to me!\n\nI am also very proud that I have been with Airbnb since 2009 and have also been a \"SUPERHOST” since the program was created.  In July of 2022, I earned Superhost status for the 33rd consecutive time.  \n\nAirbnb is, without a doubt, the BEST vacation rental platform of all!  \n\nWhen Airbnb was first created, I was one of only eight listings in all of Maui.  My claim to fame is that Brian Chesky, himself, actually called me.  Brian is one of the three Airbnb founders.  Back then, I had also listed my car on Airbnb, and Brian said I was the first person to ever list a car on his platform.  He thought it was interesting, and we had a very nice telephone conversation.\n\nBy the end of 2020, Maui had over 18,000 vacation rental listings.  The Airbnb platform has grown exponentially.  I am truly honored to be a part of it all; both as an owner and as a property manager and will continue to tell everyone how wonderful Airbnb is for hosts and travelers alike.\n\nEverybody, no matter where they are, can live ALOHA.  Maui is a great way to unlock the positive spirit within all of us.....\n\nMake it a great day!\nTracy O'Reilly R(S)\n~ Owner of Tracy’s Tropical Treasures #1 through #4 (these are my personal vacation rentals which were Named the #1 Vacation Rentals in all of Maui for both 2016 and 2019, and then named #1 in Kihei in July of 2022 for the fourth time)\n~ Hawai’i Realtor \n(Licensed on all Islands)\nLicense #73694\n~ Property Manager/Island Contact for other vacation rentals in South Maui\n\n",
