    name: 'Ilikai, stunning Oceanview in sunny  Waikiki',
    description:
      'LUXURY OCEAN VIEW<br />High floor luxury condo with superior views of the Pacific Ocean. <br />The balcony is spacious • Full Kitchen in 500 sq ft interiors with 120 sq ft lanai.<br /><br /><b>The space</b><br />The Condo, is located on the 21 floor of the iconic Ilikai Building in Waikiki, Honolulu, <br />Offers spectacular, direct views of the ocean, Waikiki beach, Duke Kahanamoku Lagoon and Diamond Head.<br />You can actually watch the surfers riding the waves from within the condo or while relaxing outside on the lanai.<br />Friday Fireworks are back!, from the lanai of thge condo you have a front seat to the show.<br /><br />The location is great; The Ilikai is one of the few ocean font properties on Waikiki beach.<br />Recently renovated to the highest standard, the condo offers luxury 5 star hotel specious room at a very reasonable price. <br />Full Kitchen which includes everything you will need in order to cook or prepare a meal ;<br />full size refrigerator, oven-stove, micro',
    neighborhood_overview:
      'Located just outside of Ilikai Hotel & Luxury Suites, this stretch of pristine white sands and sparkling waters is a perfect spot for sunbathing, swimming, and water activities. The lagoon features a state-of-the-art water circulation system and its calm conditions make it perfect for novice swimmers. Duke Kahanamoku Beach was recently named the #2 beach in America by Dr. Beach.<br />World-class shopping is only a 5 minute walk from Ilikai Hotel & Luxury Suites. Ala Moana Center truly has everything for anyone. From luxury brands to casual surf wear, there is something to pine for in this expansive indoor/outdoor mall. With three floors of shopping, dining, and entertainment, you can spend all day and night at this popular retail and entertainment destination.<br /><br />((PHONE NUMBER HIDDEN)<br />Mon-Sat 9:30am-9pm; Sun 10am-7pm',
