    description:
      '30 day minimum rental. Dwell in bliss like a local in north shore nirvana. The ultimate retreat for surfers, yogis and nature lovers, this jungle bungalow overlooks one of the most famous surf spots on the north shore. <br /><br />Climb up 80 steps winding through lush foliage to your treehouse overlooking one of the most famous surf spots on the north shore - a bit of a hike but well worth the views!<br /><br />TA Registration number: TA–139–039–9488–01<br /><br /><b>The space</b><br />The ultimate retreat for surfers, yogis and nature lovers, this jungle bungalow overlooks one of the most famous surf spots on the north shore.  Climb 80 steps up a stone path winding through lush foliage to your tree house nestled among black lava rock, shaded by deep green canopy of Banyan, mango, avocado trees. Fall asleep to the lullaby of the waves and awaken to the sweet songs of birds in the trees.<br /><br />The Unit:<br />The unit itself is 250 sq. feet, loft above with full size cotton futon, ',
