    host_about:
      "We are a wife and husband couple in our early 60's, parents of 3 splendid adults, all living in the Hawaii island chain. We share the love of communication and hospitality with world travelers in this tropical landscape. Living and working in paradise, we support a large animal family, and  grow infamous Kona coffee cherries(beans) in late summer. We also grow Macadamias, Jack fruit, Bread Fruit(Ulu), avocados, citrus, and enough bananas to keep us in pies, bread, and smoothies all year! Currently we support the Ulu Cooperative locally, where they turn bread fruits into gluten free flour, and have created fresh to freezer bags of several local (meatless) food staples. They are available now in the freezer section at Longs CVS. We welcome your interest and questions, and to stay here in our Hawaii home. Join us!",
