    source: 'city scrape',
    name: "Heather's Ohana: Clean, comfortable, and cute!",
    description:
      'TA #136-997-9392-01<br /><br /><b>The space</b><br />A cozy one bedroom, one bath cottage with an open floor plan is your home base for exploring the Kalapana Coast on the Big Island. The manager and local artist and real estate agent Heather, lives in the lower level of the main house next door.  Her fiance Ron lives upstairs in the main house.  Heather and Ron will be your source for how to get the most out of your stay.  Their dog Lupita also lives at the house, she is an adorable and friendly chihuahua. <br />    The Ohana accommodates up to two persons with a full size bed in the bedroom and a small convertable futon in the living room.  The kitchen is equipped with dishware, pots and pans, etc. and has a microwave oven, coffee maker, gas stove and refrigerator.  There is a  water filtration system so you can drink out of the faucets; the house as well as the whole neighborhood uses rain catchment water only.  The entire house is on solar power.  Parking is provided just outside y',
    neighborhood_overview:
      "Kalapana Seaview Estates is eclectic.  Retired artist hippies, gardening enthusiasts, many musicians and all kinds of creative types live here.  You'll see a good variety of interesting housing structures.  The whole neighborhood is using rain catchment water.  Many homes are on solar, and some use both solar and power grid electric.   We are affected by both droughts and weeklong down pours of rain.  Couque frogs sing in chorus thoughout the night.   There are a lot of bugs!  An occasional peacock or wild pig will be sighted, many mongoose, as well as cats and dogs and a couple of cows. The Seaview lawn has an amazing ocean view and many people enjoy the ocean breeze and sunsets and moonrises.   There is a large musical gathering at the Seaview lawn every Sunday from 4-9, with venders selling food.",
