    source: 'city scrape',
    name: 'Enchanting Rainforest Hideaway on 2 Private Acres',
    description:
      'The Enchanting Rainforest Hideaway is a  very private, well equipped, custom vacation home which stands alone on 2 private acres surrounded by a lush tropical rainforest which gives you the experience of old Hawaiian style simple elegance. <br />The cabin is the only dwelling on the property and no one else lives or works on the 2 acres.<br /><br /><b>The space</b><br />The Enchanting Rainforest Hideaway is a beautiful custom, private, vacation home/ cabin on 2 private acres in a rainforest setting all for yourself with no one else around or living on the property, its just you!  <br /><br />The 600 sq ft, well designed,  tiny house is romantic, peaceful and clean. Perfect for honeymooners, babymooners, personal retreat, eco-travelers, yogis, adventurers, or anyone who just wants to get away from it all, but still be close enough to all that the Big Island has to offer.<br /><br />     We are centrally located in Puna, 15 miles south of Hilo, a 5 miles outside of the quaint town of Pah',
