    host_url: 'https://www.airbnb.com/users/show/451536',
    host_name: 'Connie And Andrew',
    host_since: '2011-03-19',
    host_location: 'Hawaii, United States',
    host_about:
      'We are a fun loving, adventurous couple,  living in the jungles of east Hawaii.  We have traveled, and travel,  extensively. We love visiting exotic locations and meeting people from all over the world.    Connie is a semi- retired, celebrity wardrobe stylist originally from Hollywood.  Andrew, originally from Australia, is a building contractor and FEO Officer/tanker driver for the Hawaiian Paradise Park Volunteer Fire Department . Connie currently  is a yoga instructor who runs her own studio on a 40 acre permaculture farm in Pahoa and leads yoga retreats on the island of Paros, Greece during the summer. Choosing a simpler life, we moved to Pahoa in 2001 to fulfill our dream of off-grid, sustainable living.   Our home is  up the road from the Rainforest Hideaway on a separate property we developed ourselves with machetes and a lot of sweat.  We hand built our rammed earth home, made from volcanic cinder and inorganic soil from old sugar cane fields,  and grow most of own own food organically.   \n',
