    name: '~Seaside to Summit~Kona Hawaii USA~',
    description:
      "Aloha Friends!  Welcome Remote Workers and IronMan Triathletes! <br /><br />The legendary IronMan course takes athletes through the barren lava fields and calls upon athletes to find the spirit of aloha within themselves!<br /><br />Welcome to Hale Ho'okipa ~ House of Rest and Refreshment, only a short drive to the shore and summit! And where internet speed is always HIGH!<br /><br />E Komo Mai ~ Come visit us!<br /><br />Aloha nui,<br />Trish<br />Ask me about my two vehicles - available to guests at discounted rates.<br /><br /><b>The space</b><br />***Welcome to Hale Ho'okipa (House of Rest and Refreshment)!***<br /><br />*The unit is attached to the main house, yet fully private with its own entrance. It consists of one bedroom and one bathroom with shower. The bedroom and bathroom are on the same level and are kept cool with an air conditioning unit. The AC is adequate for use in the bedroom and bathroom only. Just down stairs from the air conditioned bedroom is the living room ar",
    neighborhood_overview:
      "We are located in a safe residential neighborhood. There is some road noise, though the large front lawn is normally a good buffer. Other sounds are the occasional rogue rooster, wild birds - cardinal, myna, and papaya. And there is one rascal, over friendly cat that lives on the property.<br /><br />We are 5 to 10 minutes from favorite restaurants like:  Huggos on the Rocks, Big Island Grill, TK Noodle House, Umeke's, Fumi's Kitchen, Lava Java, and Fish Hopper to name a few. Also a number of awesome wifi cafe's like: Papa Kona, Kona Coffee and Tea, and Kona Haven. We are 10 minutes from the King Kamehameha Hotel where the best Luau (Hawaiian Feast) and Polynesian Review is held every week. This landmark hotel is also host to the annual Kona IronMan Triathlon World Championships, with several historical sights on the property.",
