      'A Wonderful bright cheery room to rest between your Oahu Adventures. On the 9 the floor you are only one floor from the amazing views on the 10th floor sun deck. This room is the best kept secret on Airbnb!!!<br /><br /><b>The space</b><br />***********"HAPPIEST SPACE ON EARTH"************* <br /><br /><br />Usually rents for $189<br /><br />IMPORTANT: Sometimes the exact location of this space is not accurate on Airbnb map. We are very close to the beach!!! Please note that the first 11 pictures are from the 10th floor sundeck not from the unit. We want only pleasant surprises when you arrive :)<br /><br />As you enter the lobby decorated with colorful Hawaiian flowers and spiritual Buddha statues, you feel excited as you enter the newly installed elevators (2) which are trimmed in beautiful Hawaiian wood. You are quickly transported on to the ninth floor. There you turn right and see unit #917, slowly open the door and feel a bright happiness come over you. <br /><br />One floor away',
    neighborhood_overview:
      'Waikīkī (/waɪkiːˈkiː/; Hawaiian: [vɐiˈkiːˈkiː] or [wɐiˈkiːˈkiː]) is a beachfront neighborhood of Honolulu, on the south shore of the island of Oʻahu, in Hawaii, United States. Waikiki is best known for Waikīkī Beach, the white sand beach shoreline fronting the neighborhood.<br /><br />Waikīkī is home to public places including Kapiʻolani Park, Fort de Russy Military Reservation, Kahanamoku Lagoon, Kūhiō Beach Park, and Ala Wai Harbor.',
    picture_url: 'https://a0.muscache.com/pictures/87995735/a01c227e_original.jpg',
    host_id: '85234',
    host_url: 'https://www.airbnb.com/users/show/85234',
    host_name: 'Kurt',
    host_since: '2010-02-25',
    host_location: 'Honolulu, HI',
    host_about:
      'Performing & Visual Artist. I design all my spaces to reflect the beauty of Hawaii. Refreshing, bright, colorful & inviting.\r\n\r\nI love to travel and have visited 85 cities & 30 countries including a recent ATW (around the world) trip in (Phone number hidden by Airbnb) Honolulu - Bali - Mumbai - Istanbul - Santorini - Dubai - Rio - Machu Pichu (Cuzco) - Honolulu\r\n\r\nI share my home, which I named ArtZone, with the art community here in Hawaii. It is now open for reservation, please see "Private Art Gallery Theater Home" - The most unique listing in Hawaii !!! Its Hawaii\'s mini Guggenheim \r\nhttps://www.airbnb.com/rooms/65699\r\n\r\nNewest Listing -"The Absolute Best in the Waikiki Grand" - The most spacious studio in the building with double the best views.\r\nhttps://www.airbnb.com/rooms/7073250\r\n\r\nCOMING SOON: \r\n2 art studios in Honolulu next to my private art gallery / performance space (June (Phone number hidden by Airbnb) \n\r\nTo fellow hosts : Will treat your place like mine.  \r\n\r\nLIKES: Creating visual & performance art, Barns & Nobles, Blade, Zoe, Mikey (my doggies), the Greek Islands, Tennis, The "Stage" (favorite restaurant in Honolulu), Please ask for other favorites,...Plants & Zombies 2 (addicted), Real Time with Bill Maher, The Amazing Race, 60 Minutes, America\'s Got Talent, Love Affair, Avatar, The Notebook, Somewhere in Time, Titanic, Serendipity, "Inspiration" (favorite furniture store in Honolulu), Green Palm Trees, Marcus Miller grooves, Vine ripened tomatoes, Arugula, Tesla model S, Starbucks\' Earl Grey Black Tea (Let\'s chat?), Miami Basel, MOMA, Guggenheim, Chelsea Art Galleries, Green Tea Ice Cream.\r\n\r\nPurpose in life: To maximize the joy in my life and everyone in it :)\r\n\r\nTalk soon...... aloha,\r\n\r\nKurt',
