    host_name: 'Aaron',
    host_since: '2019-06-03',
    host_location: 'Kihei, HI',
    host_about:
      'Traveling on company business trips five days a week for eighteen years has taught us what is important to guests. This inspired us to create the ideal environment and provide the right amenities for the perfect stay.  Happiness can be about the journey that produces it and we found it on Maui, the “ valley island”.  After visiting Maui for many years on vacation for a break from business travel, we moved here permanently a few years ago. We chose Kihei, a relaxed ocean front surfing community that thrives with the aloha spirit! We welcome you into our Ohana.',
