    description:
      'Our Studio has ceramic tiled floors and a large porcelain tiled shower. All the ceiling areas have been filled with insulation and faced with 2-ply bamboo. Our studio is located at Sandpiper Village I. The Sandpiper grounds boast beautiful mature landscaping and a recently renovated pool complex with a large tile-bottomed pool plus a good size jacuzzi and two Webber gas grills. My studio is less than a minute walk to the pool.<br /><br />HI TAX ID: TA-209998-0288-01<br />HI TAX ID: GE-209998-0288-01<br /><br /><b>The space</b><br />You will find our rental rates on Airbnb to be very competitive and include Hawaii taxes. Our rates are also structured with discounts based on length-of-stay beginning with seven nights or more.<br /><br />Our Studio has ceramic tiled floors and a large porcelain tiled shower. All the ceiling areas have been filled with insulation and faced with 2-ply bamboo. <br /><br /> Our studio is located at Sandpiper Village I within the Princeville Resort. The Sandpi',
