    host_about:
      "My wife, Rachel, and I first visited Kauai in the early eighties and just got hooked. We bought our first vacation rental property in (Phone number hidden by Airbnb) at Sandpiper Village in Princeville and spent about 5-1/2 months renovating it.  We dreamed that one day we might be able to make Kauai our permanent home, and we were fortunate to actually make this happen in January of (Phone number hidden by Airbnb) We bought our second vacation rental property at Puamana in August of last year (Phone number hidden by Airbnb) , as our second vacation rental property. and after a renovation period, placed it on line as a 3 bedroom 2 bath townhouse vacation rental.\r\nMy wife still works as a flight attendant and I am retired (for all the guys out there, I'll be writing a book soon about how to make that happen).  However, I keep busy managing both properties, when we're not dancing, playing softball/tennis, or at the beach.  One of the advantages of living here on Kauai and offering vacation properties for rent is that any issues with units that may arise are addressed immediately and decisively through direct communication between the guests and me, the owner. There is no go-between management company in the loop to delay any action that may be required.  It's a win-win for everyone.\r\nMahalo for visiting AirBNB and if you have any questions, call me at home (Phone number hidden by Airbnb) or cell (Phone number hidden by Airbnb) \nKarl & Rachel Forer\r\n",
