    description:
      'Leisurely rest in this romantic, privately gated custom home. With open beam ceilings throughout, feel at home with two large, comfortable bedrooms, each with a private bathroom, only a breath away from Kealakekua Bay! The huge, open lanai is furnished and comfortable for entertaining.<br />Kayak rentals are available down the road by the ocean to explore the bay, view the Captain Cook monument and snorkel the beautiful underwater ocean sanctuary which is teaming with colorful reef fish, turtles...<br /><br /><b>The space</b><br />For a quiet getaway to Old Hawaii, visit our cozy yet classy, comfortable beach home at Manini Beach, the South side of Kealakekua Bay where world class snorkeling, swimming with the dolphins and green sea turtles are enjoyed daily.  Hikes and historical sights and adventures are all within walking distance. 2 minutes walk to the mouth of the ocean.  Enjoy plenty of parking, internet, and many fruit trees surrounded by tropical flowers.<br /><br />NOTE: There',
