    name: 'Honaunau Farm Retreat - Garden Cottage',
    description:
      "Honaunau Farm Wellness Retreat is a 7 acre organic, regenerative farm with sweeping views of Kealakekua Bay with lush tropical surroundings. The Cottage is a spacious canvas structure with an outdoor full kitchen and lanai. It has access to our retreat area with shared use of the outdoor shower, infrared sauna and yoga studio. We are 45 minutes from Kona International Airport and 10 minutes from the ocean with great beaches, snorkeling, and other activities.<br />*CAR RENTAL AVAILABLE ONSITE*<br /><br /><b>The space</b><br />The Garden Cottage has a comfortable king size bed, an amazing view of the ocean and gardens from the porch with outdoor kitchen, fully outfitted with refrigerator, stove, and kitchen appliances.   This is a very comfortable space that connects guests to Hawaii's natural environment.   <br /><br />The cottage shares use of the outdoor shower area with composting toilet.  There is also a second outdoor shower with a flush toilet under the main guest house for your c",
    neighborhood_overview:
      'We are located in a rural, tropical agricultural zone in the heart of Kona coffee and tropical fruit farmlands. With very little disturbance from light and with the wide view of the sky, viewing stars or watching the moon rise over the mountain and set over the ocean is amazing. Our area has access to beautiful beaches, hikes, snorkeling, and is near the Puʻuhonua o Hoʻnaunau National Park. We also encourage guests to visit our local restaurants, cafes and shops nearby. There are the typical day time sounds of an active, working farm with operating farm equipment, processing of agricultural products, and often hold educational tours/workshops.  The night time is also active with the sounds of cocqui frogs, roosters, and other farm animals from neighboring farms.',
