    host_since: '2010-11-10',
    host_location: 'Kealakekua, HI',
    host_about:
      'Honaunau Farm Wellness Retreat is a place where we can engage with people and share in the abundance of a co-creative conscious lifestyle.  We love meeting people from all over the world right here on the land we call home.  We enjoy engaging conversation about travel, spirituality, holistic living, healthy food and current affairs.  We find people who use airbnb to be great guests and it is a pleasure to be a host in Hawaii because we have the opportunity to extend the magic and beauty of the island to all of them in unique ways.  We love our life and we love to share it.\n\nOver the last two years, we have had our CBD hemp business, Mana Artisan Botanics, take off.  So we are now traveling often for work and have our guest experience hosted by our retreat manager.  She shares our love of people and holistic living and is a gracious hostess should you need anything during your stay.',
