    host_about:
      'KonaDave is a coffee farmer and world traveler!  His great, great, great uncle brought the first coffee plants to Kona in 1828, the rest is coffee history.  \r\n\r\nHe was born in Seattle, Washington and when not traveling the world splits his time between homes in Kona, Hawaii, and Edmonds, Washington. He travels over 100 days a year. He grew up (friends say he never grew up) in Seattle and graduated from the University of Washington before a career in the newspaper business and advertising. He worked for a Washington Post company and the San Francisco Chronicle. \r\n\r\nHe "retired" from the newspaper business when his daughter was born in 1987. When she was three months old KonaDave started traveling the world.  His daughter left dirty diapers in 24 countries before she was three. KonaDave has explored over 100 nations and states around the world. Italy is his favorite to visit.   He also runs a real estate investment company between travels.  \r\n\r\nHe loves to show people Hawaii\'s hidden treasures and his KonaZona Coffee Farm, Art  Gardens and Tiki Village.  Come visit him at the farm and he will let you pick all the coffee, fruit or flowers to take back to the condo.  He will share and cup of Kona Coffee or Mai Tai in his backyard Tiki Bar.  If you are lucky one of his famous parties will happen when you are in town.  They are legendary!!  The KonaZona is only 4 miles and 100 years away in historic Artist filled Holualoa town.  KonaDave  and the famous KonaZona Coffee Farm, Art Garden and Tiki Village  are easy to find on the internet. Check it out on your internet explorations of the Big Island!  ',
