      'Optional services offered upon request: Mid-stay housekeeping. Babysitting services provided by a First Aid and CPR certified family member. Please inquire for rates and availability.<br /><br /><b>The space</b><br />Welcome to your ocean view beach villa. The building itself is the ultimate in privacy and beauty!  The property grounds are surrounded by large koi ponds with water falls, a lagoon pool with its own sand beach for kids to play, an infinity lap pool and multiple hot tubs.  There is a  beautiful outdoor bar with cable tv for enjoying cocktails and the unforgettable sunsets. This luxury oceanfront property is landscaped beautifully with flowers, plants, palm trees and natural stones. Your two bedroom luxury villa comes with top of the line appliances, including a Subzero refrigerator, Wolf Stove and wine cooler, a ROY YAMAGUCHI Signature Kitchen – created by Hawaii’s own master chef.  The lanai is your own outdoor living room with spectacular views of the pacific ocean.  Sho',
    neighborhood_overview:
      "The Beach Villas at Ko Olina is located on the West side of O'ahu along two miles of Oahu's most pristine beaches in the world-class, private Ko Olina Resort vacation community. This resort community is home to the Aulani Disney Resort, the JW Marriott Ihilani Resort and Spa, the Marriott Beach Club, Ko Olina Golf Course and the Ko Olina Marina. You are located just steps from the beach and within walking distance of all of the numerous amenities offered throughout this vacation community. The Ted Robinson designed Ko Olina Championship Golf Course is home to the LPGA Lotte Championship. There are three full service spas located in Aulani, the Ihilani and the Marriott Beach Club. There are numerous fine dining options located throughout the resort community. The world-class Ko Olina Marina offers a selection of sports fishing, coastline cruises and water activities, from seasonal whale watching and dolphin sails to snorkeling among Hawaiian sea turtles and dramatic sunset cruises. The ",
