    host_since: '2012-07-08',
    host_location: 'Honolulu, HI',
    host_about:
      "Me and my husband have been living Hawaii for more than 18 years. We invested in KoOlina 12 years ago. The Beach Villas was our home residence before our family started growing and so we decided to move to have more space.  At that time we decided to share our beautiful beach villa with you. We absolutely love the KoOlina resort community and we can truly say that this resort is a 'place of joy'! ",
