    name: 'Lani Paradise Oceanfront Retreat with Private Pool',
    description:
      "Experience Maui - on the water's edge!<br />Enjoy natural oceanfront scenery and spectacular unobstructed views from this tastefully decorated oceanfront 4 bedroom, two-story Villa and romantic oceanfront 2 bedroom Cottage overlooking Mauiʻs North Shore. <br />Located near boutique shops and restaurants of historical Paia Town.<br />Close to hiking, surf spots, and white sand beach.<br />Enjoy the sound of the waves, swaying coconut trees, and exceptional sunsets.<br /><br /><b>The space</b><br />Our well-equipped and tastefully decorated 4 bedroom villa and 2 bedroom cottage is part of exclusive oceanfront property with a private pool in a private tropical garden setting, located 1/2 mile from the charming shops, galleries, and restaurants of Paia town.  Enjoy the sound of the waves, swaying coconut trees, and exceptional sunsets while you stay in comfort and serenity. <br /><br />You will find sandy beaches perfect for sunbathing and swimming within 150 yards, and world-class surf/wi",
    neighborhood_overview:
      "Paia Hawaii is one of the communities that make up the north shore of Maui.<br />It's a funky little town which has managed to retain a lot of it's character from the sugar plantation days. Recreational activities include hiking, fishing, sailing, snorkeling, swimming, surfing, scuba diving and more. Golfers have many excellent courses to choose from.<br />Beaches in Paia<br />The best place to surf and to watch the surfers — board and/or wind — is at Hookipa, located just a couple of miles past the center of Paia on the road to Hana. <br />If you are seeking a more traditional — and safer — family-oriented ocean experience, visit Baldwin Beach Park, located on the road from Kahului as it approaches Paia.<br /><br />Sightseers can visit beaches, art galleries and the awe inspiring Haleakala National Park. The park, which covers a large portion of the island of Maui, encompasses Haleakala volcano and protects the scenic pools of Oheo Gulch, and the unique and delicate ecosystems of Kipa",
