    name: 'Dorm Style (D)',
    description:
      'The dorm rooms are extremely clean, comfortable, nautically-decorated, and suit your basic traveling needs. The dorm has 2 bunk beds, and each room can hold up to 4 people. Air conditioning. This dorm is perfect for when you are traveling by yourself but don\'t want to be by yourself. If you like to make friends with people from all over the world, in a communal setting, you will love these rooms. PRICE IS PER PERSON PER BED! PRICE IS NOT FOR ENTIRE ROOM<br /><br /><b>The space</b><br />This is not your typical "place to stay." As a fusion of all accommodation styles, we are a unique, boutique Private Lodging offering our guests the finest accommodations on the North Shore. Our rooms are rented out individually or joined in privacy for families or groups of travelers.<br /><br />What you will experience at Kalani Hawaii is an escape from the everyday rush, a peek through the palm trees to see how life exists when you\'re on island time.  We offer once in a lifetime memories, beautiful vi',
