    source: 'city scrape',
    name: 'Treehouse Ohana',
    description:
      "The Ohana in a tropical jungle setting will be your home while visiting the Big Island. We are located at 1000ft elevation, and it's a nice few degrees cooler than downtown Kona. In 10 minutes you can get to the closest beaches by car. Start your day with a freshly brewed cup of James Bean Coffee from our farm. You have your own private entrance to the Ohana and free parking for 1 car. Enjoy the jungle & animal sounds and ocean view. Soak in the hot tub, and have fun!<br /><br /><b>The space</b><br />- Convenient to everything but tucked away in a jungle-like atmosphere, this space unites the fresh-aired joys of camping with the homey comforts of a nice bed and private spaces. Please be aware that you might encounter geckos and spiders as we live in a jungle. <br /><br />- Queen size bed plus sofabed as well as one single bed in a separate sleeping area. <br />- You have your own small refrigerator, microwave and electric water kettle.<br />- One bathroom with shower and toilet<br />- ",
    neighborhood_overview:
      "- Friendly and reliable neighbors<br />- Safe and quiet neighborhood<br />- We have some big trees on our property. Rainbow Eucalyptus, Mango and Monkey pod, just to name a few. You'll be able to watch birds, look at coffee trees, pick up mangoes from the driveway and gaze at the stars at night.",
    picture_url: 'https://a0.muscache.com/pictures/8066d1ef-c79e-499b-b83d-a69665d5c707.jpg',
