    source: 'city scrape',
    name: 'Maui Condo across fm Kam II beach',
    description:
      'A welcoming 2nd floor, one bedroom 723 sq ft condo at Kihei Kai Nani complex across from beautiful Kamaole II beach, in Kihei.  It is updated, fully furnished and equipped with full kitchen, King size bed and a fold out queen sofa-bed (sleeping 4).<br /><br /><b>The space</b><br />Comfortable, fully equipped second floor one bedroom condo in Kihei, Maui, within short walks and drives to the best Maui Beaches, across the street from Kamaole II Beach, which is a wonderful sandy, shallow beach, with a nice snorkeling reef.  Two miles north of prestigious and very expensive Wilea, our condo at Kihei Kai Nani provides you and your family an ideal location for affordable accommodations on Maui, while enjoying the  best beaches, golfing, snorkeling, dining and shopping on the island.<br /><br />This is unit 252 in Building 7 in the Kihei Kai Nani Complex.  The lanai faces the North/West side of the island and has a nice view of the garden below and a partially obstructed view of the West Maui',
    neighborhood_overview:
      'The condo is located directly across the street from Kamaole 1, II & III beaches, which have been listed among the top 10 beaches on Maui.  Kihei is a relaxed area blessed with almost constant sunshine, very little rain and plenty of beautiful beaches and shops / restaurants.  No formal wear required here.   Come ready to relax and soak up the rays, swim, eat and rest.',
    picture_url: 'https://a0.muscache.com/pictures/7289132/cec2014f_original.jpg',
    host_id: '2847058',
    host_url: 'https://www.airbnb.com/users/show/2847058',
    host_name: 'Elizabeth',
    host_since: '2012-07-06',
    host_location: 'Kihei, HI',
    host_about:
      'We are a happy family who decided to purchase condos on Maui because we love Maui so much.  Thanks to the 2008 recession, we ended up buying three condos, all in the Kihei Kai Nani resort.  We found Kihei Kai Nani to be the perfect place to buy and rent out condos for various reasons:  first and probably the most important, it is located across the street from three of the best beaches on Maui, secondly, it is located in South Kihei, which is the driest, most sunny area of the island and finally, because it is in South Kihei, it is affordable for us and our guests, yet within a short distance to luxurious Wailea.  We enjoy renting out our units to guests because they all enjoy the island and everything it has to offer, as much as we do and we get to enjoy the island ourselves.  Now that we are both retired, we have been spending our winters at our condos on Maui, to enjoy the local weather and beaches and to perform necessary updates & repairs on the condos.',
