    host_location: 'Volcano, HI',
    host_about:
      "Aloha, My name is Shannon.  I am 50 something and running our family owned and operated Bed and Breakfast and vacation rental. It takes a lot to manage 6 rooms and a family of 6.  I get help from my husband Dan, daughter Everley and her fiance, Cameron. We really enjoy what we do here just helping visitors enjoy Volcano and our Big Island. We live on site with our 4 dogs,  bun bun Bella, box turtle Exodus, cat Lunette and the  countless pheasant living nearby.\r\nWe have lived on Big Island since 1992 and have been running Aloha Crater Lodge since 2007. We have learned to be eco friendly along the way by hanging up sheets to dry, solar panel to help with hot water and we take out our own garbage as well as recycle and compost. \r\nWe are involved in community, Volcano has a lot going on at our community center with a covered skate park, farmers market weekly, yoga and plenty new stuff all the time. One neighborhood church we have attended 2004, New Hope Fellowship Volcano, has been very awesome. Dan and I serve in the children's ministry, I dance hula and Dan strums the ukulele learning our favorite songs.\r\nHopefully we will get the chance to serve you here at Aloha Crater Lodge and share our Aloha!",
