    description:
      'The Hibiscus Room is one of 5 different units at the Wailuku Guesthouse, a licensed in 2000. Centrally located exploring the island is easy from this area, and is in walking distance to restaurants, a coffee shop, and other amenities. We also have a 2020 Nissan Versa for rent at affordable rates.<br /><br /><b>The space</b><br />Relax at the Wailuku Guesthouse.  We are a legally operating vacation rental with a current County of Maui operating permit.  Be sure that wherever you book has one.  The Hibiscus room is a queen room down stairs with private bath with shower and river rock floor.  This room also has a refrigerator, microwave oven and coffee maker.  We supply Hawaiian coffee and teas.  The common area of our saltwater pool also includes 2 dining tables poolside under hardwood cabanas, lots of lounge chairs for sunning poolside, beach chairs, coolers, beach towels, WIFI and parking included. Room is decorated with Hawaiian touches.  Conveniently located near beaches, airport (4 ',
