    description:
      "***Tracy's Tropical Treasures were NAMED the #1 Vacation Rentals in all of Maui TWICE (2016, 2019) and #1 in Kihei for FOUR YEARS INCL. 2022 & 2021!<br /><br />I own 4 Tropical Treasures in Maui and have been a SUPERHOST over 33 times!<br /><br />We also made the shortlist of \"Radical Businesses in the 50th State\".<br /><br />Ocean View!<br />No Resort Fees<br />Only 150 Steps to Beach<br />Free Parking<br />AC<br />Fast/Reliable WIFI<br />Pool <br />Hot Tub<br />Tennis<br />BBQ<br />TV/Cable/Streaming<br />Fully Equipped Kitchen<br />W/D onsite <br />Fully Remodeled<br />A+ ALOHA HOSPITALITY!<br /><br /><b>The space</b><br />Aloha from Maui…<br /><br />Thank you for your interest in Tracy’s Tropical Treasures for your upcoming Maui vacation.  I would be honored to assist you.<br /><br />~ Welcome to Tracy's Tropical Treasure #1 (Unit 157).  This condo is an award winning, legal vacation rental with tropical vibes and an ocean view! <br /><br />~ We are super excited that Tracy's Tropi",
    neighborhood_overview:
      'With countless captivating beaches, near-perfect weather year-round, and panoramic views everywhere you look, it’s hard to argue that Hawaii is anything other than a tropical island oasis!  The Hawaiian Islands are incredibly unique, unlike anywhere else in the world. That is clearly evident in everything from our breathtaking scenery to our all-encompassing Aloha spirit!<br /><br />Maui has been voted the "Best Island in the World" for over 23 years.  With daytime temperatures ranging from roughly 85 degrees F during the winter, it\'s easy to see why Maui is such a popular destination choice and the #1 island in the world.<br /><br />Here’s why I think the South side of the island (where Kihei is located) is the best place to stay.  South Maui really has it all!  ;)<br /><br />The sunny South shores of Kihei offer 6 miles of popular, sun kissed beaches with warm, balmy days and cool, tropical nights.  <br /><br />Typically, humidity levels in Kihei are VERY LOW and the annual rainfall ',
    picture_url: 'https://a0.muscache.com/pictures/28780099/8939e5ba_original.jpg',
