    source: 'city scrape',
    name: 'Maluhia Guest House Welcomes Large Groups',
    description:
      "Enjoy a spacious island home for your mountain adventure on Hawaii Island with your friends and family near the Hawaii Volcanoes National Park.  See the gamut of Hawaiian life in the rainforest and quaint village of Volcano.  This guest house was established in 2010.  It is a licensed Short-Term Vacation Rental unit(STVR-19-365982) and has a Nonconforming Use Certificate (NUC-19-1863) as required by the State Of Hawaii.<br /><br /><b>The space</b><br />The Maluhia Guest House vacation rental is a huge house with five bedrooms, three bathrooms, and enough beds to sleep 12 people.  Specializing in hosting large groups, this rental is made to let our guests enjoy the whole house, its complete kitchen and spacious landscaped yard.  It is located in a native 'ohia forest  in a quiet neighborhood in Volcano on the island of Hawaii. <br /><br />     Located on the slopes of Kilauea, it is within a five minutes drive to the Hawaii Volcanoes National Park, the Volcano Art Center Gallery, the Ki",
