    host_since: '2012-07-28',
    host_location: 'San Francisco, CA',
    host_about:
      "Hi, I'm Jim Tierney. I live most of the year in San Francisco and escape to Maui whenever I get tired of the foggy weather. I run a small software company and have the good fortune of being able to work anywhere there's an internet connection. Luckily that's the case with Maui, so whenever I get an urge for whale watching, diving, or laying in the sun it's no problem coming over. I love diving and the diving in Maui is spectacular, there are some really beautiful spots. I also have a passion for photography, particularly whales and outdoor scenes, so you'll see some of my work on the walls of the unit.\r\n\r\nI hope you'll check out my place. I put a lot of work into it making sure it was a livable space for when I'm there. So I think the standards I have for the condo is much higher than most rental units.\r\n\r\nI bought this because I'll be there about two months of the year and I wanted something on the ocean. A friend of mine lives in the same building so I was very familiar with the property and all it offered (ok, so mostly it offered the ocean about 20 feet way :-). It's amazing to watch the sun set during the summer, see the whales during the winter, and listen to the waves all year long.",
