    name: 'A slice of Aloha in Historic Downtown Kona Hawaii',
    description:
      'Approved for COVID short term & 14 day quarantine rentals.  400 sq ft condo in the heart of downtown Kona.  Hawaiian decor & an eating lanai overlooks the pool.  1 queen bed, sleeps 2.  Small complex on Alii Drive has pool, hot tub, cable & wifi in unit.  A full sized refrigerator & kitchenette w/ microwave, coffee & basic utensils.  Location a "10" just steps to ocean, eats, pubs, stores.  FREE parking.<br /><br /><b>The space</b><br />Aloha!  We are an approved Short Term Rental.  We are 98 miles from the Volcano on the opposite side of the island with blue skies, ocean breezes, and beaches to explore.  <br /> If you are looking for a clean contemporary  400 sq ft studio condo in the heart of downtown Kona where a single person can be alone but feel welcome or a couple may want to enjoy each other\'s company then this AirBnB Slice of Aloha is for you!   Our location is a "10" and our price per night is lower than many of the local condos and hotels including the Marriott Courtyard, Sh',
    neighborhood_overview:
      "Walking to everything in downtown Kona including coffee, markets, pubs, restaurants, shopping, day spas etc. <br />The Islander has it's own small beach across the street but it is not a great swim beach at all, but you can view sunset and turtles with a keen eye.<br />Best swim beach in walking distance (5 minutes) is at Kona Pier in front of the King Kam (Courtyard) hotel.  Paddle board and water sports equipment rentals available there.   I go there daily when on island for a refreshing dip.  You can also swim the Ironman course from the Kona Pier and many like minded swimmers going out between 6AM and 8 AM.  <br />The neighborhood is VERY safe for Hawaii and everybody walks around and feels the aloha spirit in downtown Kona.",
