    name: 'Peaceful & Sweet Studio Style Condo',
    description:
      "The “Hala Hale” is a studio style condo with kitchenette and full bath. <br /><br />This is a charming, top story, studio style condo with vaulted ceiling, kitchenette (sm. fridge, counter with cooking space, sink & induction cook top),  balcony. <br /><br />Convenient to all beaches and island adventures and still walking distance from Princeville shops. A perfect, peaceful  launchpad for your island adventures. We provide local advice & all amenities you need. Come enjoy our condo. We're excited to host you!<br /><br /><b>The space</b><br />Private, 1 Bedroom, North Shore Condo. FULLY EQUIPPED AND CLOSE TO EVERYTHING! This is the best North Shore location possible! <br /><br />We provide you with everything you need, making it easy to take advantage of all the spectacular wonders of the North Shore, which are all within minutes of this prime location. <br /><br />This one bedroom, bungalow-style condo is newly remodeled and clean. The condo has nearly every amenity: a good sized kitc",
    neighborhood_overview:
      "Princeville is an amazingly convenient and beautiful community on the North Shore. We absolutely love it and here's why:<br /><br />From our location it is easy to walk to the beach or the grocery store and the views in either direction are spectacular, with ocean one direction and mountains and waterfalls the other! In fact there is a network of fantastic walking and running trails all over Princeville, making it likely the safest and easiest of locations of the island to walk or take a run. Another bonus is that all the nearby golf courses are open for pedestrian exploration after 5pm, which is beyond excellent for sunset strolls.<br /><br />The coastline below Princeville provides some of the most pristine, clear water due to the lack of rivers and fresh water runoff. Because of these, Princeville has some amazing exploring to enjoy below its bluffs (surf conditions permitting!!). The clear water provides some of the very best turtle viewing, snorkeling, pristine coves and nooks and",
