    source: 'city scrape',
    name: 'Amazing Oceanfront - Kona, Hawaii!',
    description:
      "Amazing ocean/ beachfront in Kailua-Kona, Hawaii.  Located in the 15+acre Keauhou Kona Surf & Racquet Club we enjoy close proximity to ocean, golf, shopping, etc! It's all here and wonderful.  With 3 tennis courts and a fabulous pool - it's perfect!<br /><br /><b>The space</b><br />The nightly rental rate does NOT includes 14.42% taxes for Ironman (October 1-15th and December 15-March 31st timeframe).  These taxes will be billed separately through AirBnB resolution center prior to arrival.  <br /><br />Our home has spectacular views indoors and out! Enjoy all the upgrades in this beautiful unit. No expense is spared when it comes to repair or updating..we want the best for our guests!<br /><br />Lay in your king pillow-top bed and watch the moon over the water or the sun sparkling on the waves as you wake up. Dine while you listen to the sounds of the surf and watch the gorgeous colors of the sunset. Enjoy watching the turtles and yellow tangs in the surf from your master bedroom wall-",
    neighborhood_overview:
      'This unit is located in a highly desirable 15+ acre oceanfront complex on the Kona coast.  The surrounding units are occupied with both year-round residents and vacation visitors.  There is always a happy atmosphere and people are very welcoming and ready to chat or share information or experiences.  We fell in love with this complex as guests many years ago and enjoy returning year after year.  The pool is arguably the best on the coast and the tennis courts are the same - wonderful!',
    picture_url: 'https://a0.muscache.com/pictures/32f96e32-a744-4e29-bbc8-dfbddee22f68.jpg',
