    name: 'Tiki Studio With Views of the Ocean',
    description:
      "STATE OF HAWAII TA-193-891-1232-01<br /><br /><b>The space</b><br />With tall open beam ceilings, and a beautiful view of the ocean, the Tiki room is a favorite Kona vacation rental. Located in the back of the property farthest from the road, the Tiki room is warm and inviting and you'll love the tropical ocean and mountain breezes that make this room such a delight. Each day the gentle breezes start the day coming in off the ocean and rising up Hualalai mountain (where the legendary Kona coffee is grown), and each evening they gently move back down it's slopes. There are times when you can hear the whales slapping the ocean with their fins as the breeze carries those sounds with it. It also carries the beautiful smells of Hawaiian tropical flowers. There are many things to do in Hawaii, and this Kona rental puts you close enough to walk to beaches, shops, ocean activities, the Kona farmers market, etc. Yet you'll find that your far enough away to still enjoy relaxing after all the fun",
    neighborhood_overview:
      'We are within walking distance to Kona town. It is about a 10 minute walk. <br />It is approximately a 20 minute walk to the pier where there is snorkeling, swimming, tours, kayak and paddle board rentals.',
