    host_about:
      'Aloha! We are originally from Washington State where we lived in a small town. One day we decided to sell everything and move to Kona and start a vacation rental. Our goal has been to make the place a home away from home for travelers. We want everyone to feel comfortable and enjoy their stay in Kona. We have met so many interesting and wonderful people along the way as well that have made it such a great adventure.\r\n\r\nWe like to spend time at the beach, snorkel, boogie board, hike and go site seeing to name just a few. One of our favorite times during the year is Iron Man. There are so many things to do in Kona this is definitely the place for us. We hope you come visit us soon. ',
