    description:
      "This lovely townhouse is only 60 feet from the shoreline & our recently renovated pool.  Sounds of the surf and birds greet you indoors/out.  We have many lovely repeat guests who want well-maintained and lovely accommodations and return each year!<br /><br />*** Portable Air- Conditioning (AC) is available in KING bedroom - additional unit possible in 2nd bedroom - $10/night; ask!  <br /><br />Twins or King bed configuration in 2nd bdrm - Ask!  $25 Change Fee <br /><br />Dedicated parking spot for our guests next to unit!<br /><br /><b>The space</b><br />Enjoy the luxury of this delightful two bedroom/two bath fully furnished condo that's gorgeously landscaped and less than 60 feet from ocean and pool.  It's the only unit like this in the complex!  The tropical setting and views and the sounds of surf will offer all the comforts of a hotel plus plenty of space and the added convenience of allowing you to prepare your meals in a fully prepped kitchen with refrigerator, dishwasher, micr",
    neighborhood_overview:
      "An idyllic 15+ acres of botanical and tropical bliss with sounds of the nearby oceanfront/surf drifting in the window.  We're so close to the water here that there's salt on every surface.  The proximity to the ocean, pool, BBQ are exceptional and nearby stores, restaurant AND the best snorkeling in Kona make this home a perfect vacation rental place.",
