    source: 'city scrape',
    name: 'Maliko Retreat',
    description:
      'This eclectic antique Hawaiian cottage is perched atop a scenic jungle gorge with breathtaking views in every direction. Open and breezy; attention to detail shines from every facet.<br /><br /><b>The space</b><br />This historic cottage is completely private and provides accommodations for two couples or a small family. Keeping in mind that this is a cottage and not a full-size house. Couples will be sharing a bathroom. And the walls are not soundproof! The upstairs bedroom has one entire wall of windows that view the ocean, the valley and the stars at night. It is open to the living room below. The downstairs bedroom door is an antique leaded glass door you can see through. (If you’re looking for ultimate bedroom privacy this may not be the right fit for you.)<br /><br />"Light and airy," "magical," "incredible," are just some of the words used to describe this unique home. Many guests check-in and then never leave the house and grounds until their check-out day. This 20 acre shade-g',
    neighborhood_overview:
      "Maliko Retreat is 3 minutes from the historic town of Makawao with great restaurants, shops and galleries. <br />8 to 10 minutes from the surfing town of Paia, with more great restaurants, shops, galleries and great beaches. Also the location of Mana Foods, a wonderful gourmet/organic food store. <br />4 minutes away from Haiku Town with restaurants and a great workout gym. <br />Haleakala is a 35 minute drive to the summit, but it will take you longer, pulling over to take photos of all the great scenic views along the way.<br />Ulupalakua is 35 to 40 minutes away, but it will also take you longer than that to get there as you stop to take photos and just to breath it all in.<br />Hana is a 2 hour drive, same thing, lots of stopping to smell the wild ginger and photograph the waterfalls along the way. It's not to be missed though. The largest archeological site in all of Polynesia is located near Hana. You could easily be the only people in the park to view this incredible, powerful p",
    picture_url: 'https://a0.muscache.com/pictures/6733946/7e7c7ae7_original.jpg',
    host_id: '1547207',
    host_url: 'https://www.airbnb.com/users/show/1547207',
    host_name: 'Sydney',
    host_since: '2011-12-29',
    host_location: 'Makawao, HI',
    host_about:
      "My favorite thing is sharing this beautiful valley with others. It started out 30 years ago as a refuse dump. For 150 years everyone dumped their garbage into the fresh-water stream from the lovely old stone bridge. My husband and I bought the property below the bridge along with the stream and all the garbage in it. For five years we hauled trash consisting of everything from old Model T Ford car frames to bags of dirty disposable diapers. The water was red, foamy and smelled terrible. But we finally got it clean and it's a beautiful stream with waterfalls, springs, caves and wildlife. \r\n\r\nI love to build things out of found objects and re-purpose and re-use things meant for the dump. We live on a small island and need to be more sustainable. It's a lot easier to just buy what you need at Home Depot or Lowe's but it's a lot more satisfying to give something a new life with ingenuity and hard work.\r\n\r\nI love coffee. I love growing it and I love drinking it. Also, coffee growers are a wonderful group of people. Our coffee growing community here on Maui and in Hawaii are like family. I also work as an advocate for small farmers here in Maui County.",
