      "Welcome to historic old Hawaii. Charming old refurbished COTTAGE with 3 bedrooms -delightful walled garden compound. This vacation cottage is set in a garden with 3 other cottages. It is set directly behind a renovated 1940s building with street frontage. Early bird discounts and longer stay discounts available.  <br /> <br /> PLEASE NOTE :  <br />17.96% tax on the reservation is payable directly to hosts. These are not included in the reservation fee.<br /><br /><b>The space</b><br />TAX OF 17.96% IS APPLIED on arrival. TAX IS NOT INCLUDED and is payable directly to host. It will be requested of guest at time of check in.  <br />Hilo Farmer's Market and stores very close. Vehicle isn't necessary if you stay here (but most prefer to have one to see sights). Reliable internet for those who need to work remotely. The decor features bright colors. Features 3 Bedrooms on upper level with toilet, kitchen & living room plus front lanai. Outlooks to the garden  from bedrooms and lanai.  'Bali",
    neighborhood_overview:
      "Hilo has a 'retro vibe' that is charming to many who come here. Hilo has a more than average number of artists & creatives.There is a fusion of cultures and cuisines which make for some interesting small restaurants around town and there are several places to eat and listen to live music.  <br />Browsers might enjoy the thrift stores or vintage stores. The 'famous' Hilo Farmers Market is one block from this property.  <br /> <br />Hilo is a great place for guests to enjoy slowing down & it is a great base for those with a long 'bucket list' of sights on the East side of the island:  <br />lava viewing, stargazing, botanical garden, waterfalls, Waipio valley and more. Hilo is central to all of these sights.",
