    host_id: '2128914',
    host_url: 'https://www.airbnb.com/users/show/2128914',
    host_name: 'Megan',
    host_since: '2012-04-12',
    host_location: 'Hilo, HI',
    host_about:
      "Aloha I'm Megan. Meet Jasper also. We've been working to create a colorful atmosphere for visitors that reflects the beauty of the tropics. Both of us enjoy helping guests feel comfortable so that they can relax and enjoy all the magic of this wet and wild side of the island. \r\n\r\nHilo could become known as 'the town that time forgot' because it has the flavor of an older, slower Hawaii. In that way we are pretty easy going and relaxed ourselves. We have two guest cottages and a romantic 1 bedroom apartment here at The Lotus Garden. This is a property that features two garden courtyards, stone Buddha statues, a fish pond and an abundance of tropical plants and trees. . The Lotus Garden is reminiscent of the former 'sugar cane plantation days' when houses were clustered together so that there was a sense of community in the way people lived. Here Travelers meet other travelers in passing. Another wonderful feature of The Lotus Garden is that we are right in the center of town. Guests enjoy being a block away from the Farmer's Market and within walking distance of our cafes, restaurants, galleries and even Hilo's own Kava Bar. \r\n\r\nJust ask us. .. we know some great little beach parks close by and some special things to do around Hilo..  \r\n\r\n... Our latest creed to live by? \"Now is a really good time!\" And so it is.. ",
