    source: 'city scrape',
    name: 'Studio by the Ocean.  Starry Night. Adventures',
    description:
      "A quaint and budget friendly, first level floor studio apartment, with a spacious bathroom which includes a rain shower tub. The space comes with a kitchenette; includes everything you need to make a light meal. This space is perfect for independent travelers that enjoys a space nestled in a tropical garden, rich with tropical wildlife. We are just a quick stroll to the end of the road with a breathtaking view of the majestic blue of the Pacific Ocean.<br /><br /><b>The space</b><br />Our little haven of paradise is located in a semi off-grid area in a blooming tropical garden. This unit is the first floor of a three floor structure with private entrance with your own private outside seating area (lanai). We're close enough to the ocean to hear the whales blowing and slapping their tails when in season. This 500 square foot modern studio apartment is bright, airy, and clean. The large bathroom is done in bright Italian blue tiles and features a large tub and rain shower. Our water syst",
    neighborhood_overview:
      "The neighbors and \"locals\" in Kalapana Sea View as well as the surrounding areas are relaxed, helpful, and friendly. I have found, they will always give excellent tips on the best current things to do in the local area. Don't  miss Uncle Robert's at the West end of the road for a unique island experience filled with music, dance, food and local crafts on Wednesday nights.<br /><br />This neighborhood is the old Hawaiian feel with overgrown trees, birds, coqui frogs and other cute tropical animals roaming the rich and lush landscapes of the east side of the island. <br /><br />Here is a recent quote from one of our guests:<br /><br />What a great find! Was looking for a quiet place close to the water and SherylRose's spot definitely fit the needs. Super quiet, well furnished and just the right mix of a short drive to anything you need to sipping coffee on the patio. Stars were fantastic and the cool ocean breeze was fantastic! Hope others find this place as refreshing! Thanks again.",
