    host_since: '2013-01-05',
    host_location: 'California, United States',
    host_about:
      'Along with trips back and forth to Hawaii from  Northern CA throughout the year. A wanderlust at heart; I\'m constantly on the move and have multiple projects going and trying to find time for new adventures. \r\n\r\nI work as a graphic designer and a freelance photographer. I also run my humble multiple unit vacation property in Hawaii. During my non busy days, I spend my time relaxing with family and catching up with friends!\r\nAlso finding time to follow my passion through modern painting and anything handmade. A soft heart for animals and mostly enjoy the company of adventure seeking long life friends and a love for laughter. \r\n\r\nFuture travel plans are investing more of my adventure time in the Big Island and explore the raw beauty of my second home. \r\n\r\n"The true fruit of travel is perhaps the feeling of being nearly every where at home." ~Freya Stark\r\n\r\n',
