    description:
      "This is a cozy, cute, and private ground level walk-in cabin surrounded with flowers and plants to sweeten the view. It is close to the community kitchen and bathrooms and has a very comfortable double mattress.<br /><br /><b>The space</b><br />KIRPAL MEDITATION AND ECOLOGICAL CENTER (KMEC)<br /><br />This is a cozy, cute, and private ground level walk-in cabin surrounded with flowers and plants to sweeten the view. Once inside, this intimate sanctuary gives you all the tranquility you need to find peace within yourself. It is close to the community kitchen and bathrooms and has a very comfortable double mattress.<br /><br />The shared kitchen and two fully equipped, clean, and modern bathrooms, also with jungle view, are about 20 feet away.<br /><br />Our spacious and open 11 acre property is surrounded by a buffer of jungle and features 100 trees with 30 or so kinds of tropical fruit. You're welcome to enjoy the freshest fruit possible whenever they're ripe!<br /><br />Enjoy a walk h",
    neighborhood_overview:
      'Due to the recent volcano eruption in 2018, we mandatorily had to leave our property on May 6th, 2018. Our property was left vacant and closed for over 6 months.  Our normal response rate before the eruption to was close to 100%.  During the eruption period when we were closed, we did not know of any inquiries and did not  respond, thus the low rating. We expect that we will be back to an excellent response rating very soon.  Our location and views are pristine. Mango Road is a sacred authentic well preserved landmark to the ancient Hawaiians and so beautiful.',
