    host_location: 'Haiku-Pauwela, HI',
    host_about:
      "We are Gary & Marianne Callari - \r\nWe offer for rent private rooms in Our 1929 Farm house on Maui, Hawaii and (2) Casita Studios in the Red Rocks of Sedona, Arizona. We love nature, home and family. Our fun and together past time is making great dinners at home. We grow our own food and eat and live a very healthy plant based diet. We go to bed fairly early and we get up early. We have healthy habits and healthy relationships with everyone we come in contact with. An open mind and a positive attitude we believe have been our tools in getting the home and lifestyle we create. We try not to have to many rules and flow with what life presents us. Sometimes we are really good at it! We believe energy follows thought, so if you think you can you can! We love to travel and enjoy meeting people from all over the world. Our travels have brought us to Spain, Italy, Mexico, Thailand and the Caribbean. We use travel guides about food to experience the flavors of live! We have two grown adults that we are very proud of, we honor who they are and the choices they have made and look forward to many more exciting adventures with them. 5 things we can't live without: Great weather, great food a higher consciousness, good organic chocolate and of course an active healthy body! We subscribe to the 4 agreements: Be Impeccable with your word. Don't take anything personally. Don't make assumptions. Always do your best. We wish you Happy Travels and Aloha! Gary and Marianne",
