    name: 'The Tranquil & Airy Rollinia Cabin',
    description:
      "Catch your breath from the hustle and bustle in the serenity of our charming Rollinia Cabin, offering a queen bed, lovely furniture and a bunk bed. Great for families!<br /><br /><b>The space</b><br />KIRPAL MEDITATION AND ECOLOGICAL CENTER (KMEC) <br /><br />Catch your breath from the hustle and bustle in the serenity of our charming Rollinia Cabin, offering a queen bed, lovely furniture and a bunk bed. It can accommodate up to four people.<br /><br />The shared kitchen and two fully equipped, clean, and modern bathrooms, also with jungle view, are about 50 feet away.<br /><br />Our spacious and open 11 acre property is surrounded by a buffer of jungle and features 100 trees with 30 or so kinds of tropical fruit. You're welcome to enjoy the freshest fruit possible whenever they're ripe!<br /><br />Enjoy a walk half a mile away to the newest formed beach in the world Isaac Kepo'okalani Hale Park. You will walk down Pohoiki road which is full of huge old mango trees that make for a stun",
    neighborhood_overview:
      'Our location is pristine. Mango Road is a sacred authentic, well preserved landmark to the ancient Hawaiians and so beautiful.',
    picture_url: 'https://a0.muscache.com/pictures/103098015/8bef1e86_original.jpg',
