    description:
      'Beautifully designed custom home in Upcountry Kula with panoramic views of the coasts,  West Maui mountains, and neighbor islands. The perfect location for peaceful relaxation while you enjoy your stay in Maui and everything it has to offer.<br /><br /><b>The space</b><br />Welcome to this tastefully designed Japanese modern home in the Upcountry of Maui. Located at 3,000 ft. on the western slope of Haleakala. My home offers a serene environment with spectacular views of the north and southwest shorelines, the West Maui mountains, and beautiful valley below. This is a perfect location to plan your day trips all over the island and such a peaceful, relaxing place to come home after your activities.<br />The house is fully furnished with 2 bedrooms each with a queen sized bed, gourmet kitchen, TV, Bose stereo, wireless internet, rain shower, washer and drier, outdoor dining area - all the comforts of home.<br /><br /><b>Guest access</b><br />Not only will the entire home be at your dispo',
