    host_about:
      "After selling my Veterinary practice in Southern California, I moved to Maui in 2005. I  enjoyed occasional relief work at the Maui Humane Society doing surgery for the spay and neuter programs but have completely retired from all veterinary activities in 2017.\nAs a clarinetist, I am active in chamber ensembles and local classical music groups as well as theater orchestra. Hosting chamber music concerts at my home and providing an Upcountry venue for wonderful musical soirees is a great pleasure.\nOver the years, I have developed a farm on my land and tend to citrus trees, avocados, dragon fruit, coffee, bananas, various vegetables and herbs. I also have endeavored to reintroduce native Hawaiian plants to the gulch on my property's border. \nCooking and entertaining are a big part of life here among friends as well. \nI enjoy traveling  especially to music festivals and large metropolitan areas.\nAs an AirBNB host, I enjoy meeting new people from around the world and offering what I can to enhance their visit.",
