    neighborhood_overview:
      'From 2018 Coastal Living article: <br /><br />"There are beach towns, and then there’s a town so stop-in-your-flip-flops gorgeous, it’s tempting to name that place the ultimate beach town. Such is the temptation of Kailua, Oahu’s slender strand along its windward shore that’s home to two idyllic public beaches and 53,000 very lucky Hawaiians.<br /><br />Kailua Beach arcs in powdery white-sand splendor for 2 1/2 miles. The gentle ocean drop-off from shore makes it perfect for swimming, bodysurfing, and surfing, and a steady trade wind also makes it ideal for windsurfing.<br /><br />Surrounded by steep emerald slopes and carved quietly by rivers that flow into Kailua Bay, this understatedly hip community just 12 miles from Honolulu is endowed with extraordinary beauty, a serene lifestyle, small-scale neighborhoods of single-family homes, a walkable downtown with great food and shopping, and vistas without peer.<br /><br />Locals maintain a tight leash on development, and the result is a ',
