      "Our cool and comfortable 2 bedroom cottage allows for privacy and quiet in the heart of Kuau.  Relax under the kukui, breadfruit and coconut trees. Enjoy some sun and refreshments  on the private patio furniture after a great day on Maui's North Shore.  Full kitchen, private barbecue, rave reviews!!!<br />TA-046-968-0128-01<br /><br /><b>The space</b><br />2 bedroom/ 1 full bathroom cottage with fold out sofa bed.  Stocked kitchen, DirecTV, wireless internet, BBQ, papayas, limes, bananas, and breadfruit outside.  Inside and outside dining area, sunbathing chaise lounges etc.  Brand new designer furnishings, teak, comfy beds ( 1 king, 1 queen) , new linens and towels.  Remodeled cottage is fresh for you.<br />Awesome location on Maui's North Shore... Walk to Beach, 1 mile from center of Paia town for shopping, dining, recreation.  1/2 mile from Hookipa. <br /> Great for small gatherings Perfect for paddlers, SUP's, kiting, windsurf gear. Gear is safe on property vs. condo complexes.   N",
    neighborhood_overview:
      'Kuau Store is conveniently located across the street.  Fresh sandwiches, coffee, smoothies, groceries for last minute needs.   Quiet neighborhood with a few kids, many active water sports enthusiasts. <br />Plenty of warm smiles and flowers are seen everywhere :)<br />I have had 2 people comment on road noise over the 6 years of hosting.  I feel it is pretty quiet but we are on the Highway allowing for proximity to beaches and Kuau Mart.  It may be louder than normal.<br />Mamas Beach is across the street behind Kuau Mart.  That is the most walkable beach access that we have.  Down Aleiki Place,  til it bends left along ocean and then bends left again.  Follow the Blue Beach access signs :)',
