    name: 'Pane Makai Poipu Beach House',
    description:
      'With ample room and durable bamboo decor, this plantation-style beach house is perfect for large groups, wedding parties and family reunions. Comfortably sleeping 14, look no further for the perfect gathering place for friends and loved ones! <br /> <br /> This 6000 square foot home is split into two identical suites. The “Turtle” and “Dolphin” suites can each accommodate 7 adults, with private living areas, kitchens that can seat 12. <br /> <br /> Just steps from the beach, enjoy panoramic ocean views from the upstairs lanai and garden views from downstairs. A historic lava rock wall surrounds the property on three sides, softened by 100-year-old Monkeypod trees and tropical foliage. Enjoy BBQs in the large yard and special access to the Poipu Beach Athletic Club! With a convenient location, spacious layout and ample amenities, this vacation rental has something for everyone. <br /> <br /> - Located in Poipu (South Shore).<br /> - Large Hawaiian-style beach house, split into two ident',
