    description:
      'Private bungalow,90 feet from Laie Beach. Near Polynesian Cultural Center. Over half mile of sandy beachfront. Great swimming, snorkeling, fishing, winter surfing & kayaking.  Perfect for romantic getaway.  Sleeps up to four guests.<br /><br /><b>The space</b><br />This Oahu beach bungalow is a stand alone studio home.  Beautifully furnished and spacious studio bungalow with a large private patio and beach access from the gardens and ocean view is only 90 feet from our sandy beach front. The Coco Bungalow is air-conditioned and spaciously fitted with island wood craft furnishings, California King Bed with high thread count linens, full size sofa sleeper, full kitchen with a range, refrigerator, and an array of cooking utensils, microwave oven, stainless pots & pans, flat screen HDTV, complimentary wireless internet, on-site laundry facility, outdoor grill and picnic area and beautiful Koi ponds. The base rate applies for up to 2 occupants. Additional occupants incur applicable per pers',
    neighborhood_overview:
      'This is rural Hawaii!  You can watch the sunrise over the pacific ocean and hike to waterfalls, within walking distance from Tiki Moon Villas.  Quiet, relaxing.  Between January and May, whales are seen almost on a daily basis from the garden.  Relax on our secluded beach and be rejuvenated.  <br /><br />Enjoy Polynesian Cultural Center during the day and  walk across the street to your room!  No need to join the traffic jam going back to Waikiki.',
