    description:
      'This beautiful property overlooks 60 sweeping miles of the South Kona coast line.  Just a mile from the famous Kealakekua Bay and Captain Cook monument, this cottage combines the best of Old Hawaii with all the modern amenities and tropical gardens.<br /><br /><b>The space</b><br />This private, secluded and charming Old Hawaii style Ohana cottage (family home) is a stand alone dwelling just 5 minutes from beautiful Kealakekua Bay,  Manini Beach, and Ke’ei Beach. The bay is famous for its incredible snorkeling, standup paddling, kayaking and opportunities to swim with the local pod of spinner dolphins.    <br /><br />The cottage consists of separate living quarters making it perfect for the longer staying guests.  A living room/bedroom with a queen-size bed, a well appointed kitchen with full size refrigerator, washer/dryer available for stays of one week or longer, bistro table, gas barbecue, covered lanai with outdoor seating, lua, and a private outdoor shower all with panoramic view',
