    description:
      'Make us your home base  for your Big Island exploration. Experience a galaxy of stars, the expansive ocean, and the creation of new earth. After a full day of adventure, return to your peaceful sanctuary and rejuvenate in perfect sleeping weather.<br /><br /><b>The space</b><br />Make us your base camp for your Big Island exploration. Experience a galaxy of stars, the expansive ocean, and the creation of new earth. After a full day of adventure, return to your peaceful sanctuary and rejuvenate in perfect sleeping weather. Get ready for your next adventure or travel deeper on your inner journey. Relax on the lanai with a cup of Kona coffee, listen to birdsongs, and simply be.<br /><br />We are centrally located on the west side of the Big Island of Hawaii between Kailua Kona and Waimea. At an ideal elevation of 2,400 feet, OP  Hale has sunny days and cooler nights. A perfect place for a self retreat or a romantic getaway, OP Hale provides a sacred space for contemplation, exploration, a',
    neighborhood_overview:
      "Puu Lani Ranch is an upscale, gated,  ranch neighborhood. There are three miles of walking/jogging paths with ocean and mountain views. Spectacular star gazing and cooler evening temperatures for a good night's sleep. <br />We are centrally located on the west side of the Big Island. You can explore the entire island from here. Kailua Kona, Waimea, and Waikoloa are all 25 minutes away. You can reach Mauna Kea in about 45 minutes, Hilo in an hour, and Volcano National Park in an hour and a half.  World renowned beaches such as Hapuna and Kua Bay are about 30 minutes away. What we love most about our neighborhood is perfect weather all year long and the ability to explore the entire Big Island from our home.",
