    name: 'Ocean Sun: 1bdrm/1ba at Tiki Moon Villas',
    description:
      "Directly on the beach front. Wide open ocean view. Greet the sun and moon rise from your private deck.  A romantic get away. Recharge yourself.  Sleeps up to four people.<br /><br /><b>The space</b><br />The Ocean Sun is directly on a sandy beachfront with views of the moon and sunrise on the ocean's horizon and is the perfect location for your wedding-moon or just extended romantic vacation get away. Right out your front door is your personal patio for relaxing in the sun or winter and spring whale watching. The Ocean Sun has a fully equipped kitchen with Viking gas stove with convection, all kitchen and cooking utensils, microwave, high end pots & pans, large refrigerator with filtered water. Designed with stunning stone counters and ample cabinetry in kitchen for extended vacations or honeymoons. Beautifully furnished with modern comfort seating mixed with century old antiques from Morocco, Indonesia and China, flat screen HDTV, streaming channels, complimentary wireless high speed ",
