    host_about:
      'I am Zoë, a Brit blessed to call the Big Island my home. \n\nOur family has stayed in many vacation rentals over the years, so we determined to set up our rentals embracing the best of our experiences, adding what we found missing, and sprinkling some original ideas into the mix. \n\nWe always ask guests to suggest what could be improved at our rentals, however small - the properties’ constant improvement is entirely due to the excellent suggestions given.\n\nWe care about our guests and delight in tailoring the space to make your visit to the Big Island the best it can possibly be. We have stayed in contact with many previous guests, often hosting them many times over the years. We really do have the very best of guests!\n\nA UK-trained psychotherapist and long-time parenting coach, business coach and wellness coach, I work with clients worldwide to increase presence in our daily lives through intensive, focused work to heal unresolved trauma.\n\nImmersed in traditional healing from an early age, I bring healing into all aspects of my life and my work. If you feel you would benefit from one-to-one sessions during your stay, please let me know with as much notice as possible, as my calendar is usually booked a few months ahead.',
