    source: 'city scrape',
    name: 'Aloha Nature Lovers',
    description:
      'Aloha Nature Lovers!  Located between Waipio Valley and Volcano National Park.  Great hiking!  Convenient and very private swimming holes to enjoy at your door.  One can spend days chasing the many private waterfalls and exploring our property.<br /><br /><b>The space</b><br />Aloha Nature Lovers!  Located between Waipio Valley and Volcano National Park.  <br />Relax or go adventuring at Aloha Nature Lovers  !!!<br />For the experienced hiker we offer endless waterfalls and hiking. <br />Bordering on each side of our 20 acre ranch property there is a half mile of very private tropical steam valleys , swimming holes and waterfalls. One can spend days exploring the pristine and endless waterfalls that continue for miles up the valley.  <br /> Enjoy each breath of pure sweet air. The little cabin has great ocean views  !!!<br /> You’ll definitely be enjoying the view of the ships sailing by , sunrises ,sunsets ,rainbows ,<br />clouds and flocks of birds parading down the coast.  Nights ar',
    neighborhood_overview:
      'Located just north of Hilo on the northeastern side of the island of Hawaii, the Hamakua Coast is one of the most beautiful stretches of scenery on the island. With ample rainfall each year, the region boasts lush tropical rainforests, stunning waterfalls and tranquil green valleys. <br /><br />Awesome ocean views.  Papaaloa Country Store , Bar and Restaurant is just down the road . Great pizza and local style fresh food.  Nightly live music almost 6 nights a week.<br /><br />Please be aware that depending on rain fall the property is at risk of drought. This can sometimes cause the pond levels to drop making it less scenic for some.',
