    name: 'Lani Paradise Oceanfront Cottage',
    description:
      "Experience Maui  -  less than 20 yards from the water's edge!<br />This cozy and romantic oceanfront 2 bedroom  Ginger Cottage has an amazing ocean view and is surrounded by tropical vegetation.<br /><br /><b>The space</b><br />Enjoy the sound of the waves, swaying coconut trees, and exceptional sunsets while you stay in comfort and serenity. This rustic oceanfront cottage is part of exclusive oceanfront property with a shared pool in a private tropical garden setting, located 1/2 mile - just 10 minutes walking distance from the charming shops, galleries, and restaurants of Paia town. <br /><br />You will find white sand beaches perfect for sunbathing and swimming within 150 yards, and world-class surf/windsurf/kiteboarding spots nearby. Conveniently located so that you may also explore the area's many other attractions such as the spectacular Haleakala crater, rainforest hikes, and waterfalls.   <br /><br />Features include high open-beam ceilings, 2 bedrooms, a fully-equipped kitchen",
    neighborhood_overview:
      "Paia Hawaii is one of the communities that make up the north shore of Maui.<br />It's a funky little town which has managed to retain a lot of it's character from the sugar plantation days. Recreational activities include hiking, fishing, sailing, snorkeling, swimming, surfing, scuba diving and more. Golfers have many excellent courses to choose from.<br />Beaches in Paia<br />The best place to surf and to watch the surfers — board and/or wind — is at Hookipa, located just a couple of miles past the center of Paia on the road to Hana. <br />If you are seeking a more traditional — and safer — family-oriented ocean experience, visit Baldwin Beach Park, located on the road from Kahului as it approaches Paia.<br /><br />Sightseers can visit beaches, art galleries and the awe inspiring Haleakala National Park. The park, which covers a large portion of the island of Maui, encompasses Haleakala volcano and protects the scenic pools of Oheo Gulch, and the unique and delicate ecosystems of Kipa",
