    description:
      'Honaunau Farm Wellness Retreat is a 7 acre organic, regenerative farm with sweeping views of Kealakekua Bay & lush tropical surroundings. The Main House has 3 bedrooms, 2 baths, dining room, living room, full kitchen & lanai. It shares use of our retreat area with an outdoor shower, yoga space & infrared sauna, & also has its own washer/dryer. We are 40 min. from Kona International Airport and 10 min. from the ocean with great beaches, snorkeling, & other activities.<br />*Car Rental may be possible*<br /><br /><b>The space</b><br />If you are looking for a unique, beautiful, and comfortable place to stay that offers endless ocean views, look no further. <br /> <br />The Main House at Honaunau Farm has 3 bedrooms with, 2 queen beds, 1 double, and a large sofa in the living room.  We can provide a couple more sleeping pads and linens for kiddos or adaptable adults which allows for the space to accommodate 8 folks family style.  (please note that there is an additional charge for any gue',
    neighborhood_overview:
      'We are located in a rural, tropical agricultural zone in the heart of Kona coffee and tropical fruit farmlands. With very little disturbance from light and with the wide view of the sky, viewing stars or watching the moon rise over the mountain and set over the ocean is amazing. Our area has access to beautiful beaches, hikes, snorkeling, and is near the Puʻuhonua o Hoʻnaunau National Park. We also encourage guests to visit our local restaurants, cafes and shops nearby. There are the typical day time sounds of an active, working farm with operating farm equipment, processing of agricultural products, and often hold educational tours/workshops.  The night time is also active with the sounds of cocqui frogs, roosters, and other farm animals from neighboring farms.',
