    description:
      "This rustic and cozy cabin sits on our 5 acre parcel in the cloud forest above Kona town on the Big Island, called Kaloko Mauka. It has it's own private little corner of the property, complete with secluded hot tub, yard and large lanai perfect for the bbq! Best for 2-3 adults. Loft can suit a child or two 8 years and older or another adult. There is a fold out futon with extra bedding and pillows. Loft railing is not completely protected for children under 8.<br /><br /><b>The space</b><br />Cozy and private cabin set off in the Kaloko woods above Kailua Kona town on the Big Island of Hawai'i (Hawai'i island). Set on 5 acres, this 600 square foot cabin comes equipped with everything you should need for a Hawaiian vacation! Large studio with queen bed, bathroom, full kitchen, large lanai with gas grill and even a washer/dryer on the side of the cabin. There is a fireplace and a wall heater for cold evenings and a double fold out bed in the small loft up above; perfect for another adult",
    neighborhood_overview:
      "Our Kaloko neighborhood is absolutely beautiful! We sit up above Kona town starting at 2000 foot elevation and we are a cloud forest; full of native birds, Ohia fern trees and are 10 degrees cooler than the humid heat of Kona. Winter is our drier months with less rain. Summer can be rainier as it is more humid during those months. Rain doesn't last too long most of the time, and the sunny beach is just 10-15 minutes away. All of the other properties on our street are at least 5-10 acres; which ensures privacy and lots of quiet.",
