    host_since: '2013-01-12',
    host_location: 'Kailua-Kona, HI',
    host_about:
      'Hi! My name is Caroline Carson. My husband Scott and I have lived here on the Big Island for 19 years. Originally from Oahu, we decided to move here to start a family and to start our own business. We have 2 children as well as 3 dogs, 6 cats and 5 chickens! We live on 5 acres in one of the most beautiful places on the Big Island called Kaloko Mauka. We are close enough to the heart of Kona, and yet removed from it all up here on the slope of Mount Hualalai. Cooler weather, lush foliage, native birds and hiking trails surround us. Come and enjoy this slice of Heaven!',
