    source: 'city scrape',
    name: 'Lucy Fan Ohana - A Maui Gem!',
    description:
      'Maui is a vacation paradise. Hawaii is one of the most healthy places on the planet. Judit and Tim have a very comfortable apartment to call home while you enjoy adventures on Maui. Please look at our complete listing before you book. Check out our great reviews.  We are glad to answer your questions about the islands. We observe high quality cleaning and procedures. Please arrive vaccinated. We are licensed by Maui County. Our permit number is BBPH 2015/0005. State Tax #026-283-6224-01. Mahalo!<br /><br /><b>The space</b><br />Aloha! My wife and I have a family size apartment for rent in a friendly neighborhood on the beautiful North Shore of Maui. The Lucy Fan Ohana is a large, clean, and private apartment.  Please look at our photo collection. The spacious second floor master bedroom has a lovely queen bed and large, covered outdoor deck. The first floor living area  includes a sofa and a futon in the living room, a kitchen and dining area for 4, and a lovely bathroom. You can see t',
    neighborhood_overview:
      'A word to the wise. A Maui County Rental Permit is essential for a stress-free vacation. Make sure your host has a valid permit number. Ours is BBPH 2015/0005. Maui has many unlicensed operators who cannot guarantee your reservation. Many are being shut down on short notice, sometimes after guests have already moved in! Yikes! Will they will be here when you arrive? We will! Plus, we have plans for your safety and a lot of experience creating guest satisfaction. Please arrive vaccinated or with a Covid negative PCR test. Please keep in mind that we are a one unit, family style accommodation  located in a neighborhood. We do not employ a large staff of housekeepers or gardeners. We are not a resort. We depend on the tradewinds for great ventilation. If you want a guarantee of no geckos, or bugs, or air conditioning, you would be well advised to book into a resort or condominium. This is a home in a humble Hawaiian style  neighborhood. We expect our guests to honor the house rules. The s',
