    name: '★Rainbow Lookout - PRIVATE HOT TUB+ACCESS TO POOL',
    description:
      '★Private Hot Tub ✔<br />★44 ft Swimming Pool (shared) ✔<br />★King Bed ✔<br />★Sunrise/moonrise View ✔<br />★Guest Laundry ✔<br />★Cliff Walk ✔<br />★High Speed WIFI✔<br />★Star Gazing✔<br /><br />This Stunning North Shore Maui hideaway is surrounded with lush fruit and flower gardens, coconut palms, banana trees, and has easy access to all there is to do on this magnificent island. Stargaze … soak in your own private hot tub with a chilled glass of wine and enjoy a once-in-a-lifetime experience. The \'real\' Maui.<br /><br /><b>The space</b><br />"Aloha" - MAUI IS WELCOMING VISITORS AGAIN with proof of vaccination!<br /><br />Situated on Maui\'s spectacular north shore, this fairy-tale hideaway is the ideal honeymoon retreat ... far away from tourist crowds, (yet is only a 20 minute trip to the nearest great restaurant or grocery store).  It\'s so lovely to come "home" at the end of a day ... and sometimes, you\'ll probably just to stay "home" and relax!  Perhaps you\'ll "eat in" ... or wan',
