    description:
      "Beautiful and private 1/1 cottage on our 5 acre property in the mountain neighborhood of Kaloko Mauka above Kona on the island of Hawai'i. Lush ferns, ohia trees and lots of bird watching from your own private hot tub and deck. Close to town, beaches and shopping. 4G unlimited internet. Quiet and secluded cottage on property.<br /><br /><b>The space</b><br />Charming little cottage in the woods of Kaloko. Located conveniently above Kona town on the island of Hawai'i (Big Island) at 2700 foot elevation, this private 1/1 house offers peaceful solitude and cool weather. Enjoy bird watching, nature hikes and the coziness this cottage has to offer. Beautiful grounds, yard and a safe neighborhood. <br />Full kitchen, full bath with tub/shower and stackable washer/dryer. Two decks, gas grill, propane wall heater, king bed. tv/dvd, beach accessories and all the amenities you'll need for your Big Island adventure! Bring along your laptop as we have fast, unlimited 4G wireless internet in the co",
    neighborhood_overview:
      "Kaloko Mauka is so lush and beautiful! Take an off road hike just up the street or tour Mountain Thunder Coffee and sample their wonderful Kaloko coffee. We are in a cloud forest, so the weather is cool. The winter months are drier than our summer months. Usually any rain showers we do get don't last all that long and they help keep our forest lush and green.",
