      'Aloha I am a restaurant owner in a rural picturesque southern Hawaiian town. Naalehu sits amid rolling hills and pastureland and is the only non developed coastline left in the Islands. I like to hike and fish and relax at home with my lovely dogs at my home in the mountains. I have fixed up the two small apartment rooms behind my now famous restaurant so that folks can have a nice homey place to stay while exploring the southern part of the big Island. Don\'t worry about food you are  right in the backyard of the famous Hana Hou Restaurant  the "HUB of Aloha" in our quaint town. Good home cookin and friendly people will make you feel welcome and remember breakfast is included in your room charge so come on in to Hana Hou and order a tasty  traditional breakfast. We also have espresso. Come on down to Naalehu and relax for a couple of days. Love to meet you. Aloha Patty  My Transient Accomodation tax number is TA (Phone number hidden by Airbnb) , ',
