    source: 'city scrape',
    name: 'Secret Falls Retreat Valley View Rm',
    description:
      "This room opens up to the solar heated salt water pool deck, has a private deck out back and a private bathroom with a very large walk in shower with 2 shower heads.  Desk, fridge, coffee pot, seating area and beautiful views of the Wailua Valley. We also have a Jacuzzi deck that overlooks the Wailua river valley with outstanding views of Mt Waialeale.  We are a long standing BnB, 25 years!. Look forward to hosting you in our little piece of paradise!<br /><br /><b>The space</b><br />The Valley View Room is one of 3 rooms in our B&B.  It has a bamboo tropical flair and amazing views of the river valley, mountains, waterfall and pool.  It's about 15' X 15' with a queen size bed, private bath, ceiling fan, tv, dvd, small fridge, private deck out back, large walk in rock shower with 2 shower heads, writing desk and a single bed if needed.<br /><br /><b>Guest access</b><br />Common areas are the living room, pool deck, all outdoor grounds.<br /><br /><b>Other things to note</b><br />All pa",
    neighborhood_overview:
      'We live in a quiet neighborhood on the Wailua rim with outstanding views of the Wailua river valley and Mt Waialeale. We are within walking distance to the Sleeping Giant hiking trail and 5 minutes from downtown Kapaa and the beach.',
    picture_url: 'https://a0.muscache.com/pictures/52506701-7dda-4d22-9edf-87d8d827e3da.jpg',
    host_id: '2657251',
    host_url: 'https://www.airbnb.com/users/show/2657251',
    host_name: 'Darcy And Tim',
    host_since: '2012-06-16',
    host_location: 'Kapaʻa, HI',
    host_about:
      'Aloha!\n\nWelcome to Secret Falls Retreat in Kapaa, on the island of Kauai in Hawaii.\n\nWe are a permitted Homestay with the County of Kauai!:).\n\nUse Permit No. U-2015-14\nClass IV Zoning Permit Z-IV-2015-15\n\nThis property formerly called Mohala Ke Ola, has been running as a BnB for over 30 years. You will find it listed in the old Kauai guide books, we renamed it in 2011 when I purchased the property. You can really hear the water falls roar on a quiet night after the rainfall, and you can hike to the falls from the back deck. Hence, what a better name than Secret Falls Retreat!. \nWe were closed for a while as we did many renovations and upgrades, but are up and running 100% now and look forward to hosting you and receiving new reviews soon!:).\n \nI am originally from California, grew up in Northern British Columbia, Canada. \nI love the outdoors and am a workaholic, always busy! Tim was born and raised in Hawaii, so you have all the local inside information you need:). We have 3 children ages 10, 14 and 18 that attend a private and public school here on Kauai.  They help out with the BnB when time allows between their studies and extracurricular activities.  We enjoy the beach, hiking, horseback riding, paddle boarding, beach Volley Ball, fishing, and soccer. We have Gypsy Vanner horses, goats, chickens, ducks, pheasants, pigs and bunnies at our other property just down the road that our guests find great fun in touring!\nAs we love music, we also own an event company called Hawaiian Muzik Factory, so we do weddings, anniversaries, baby parties, graduation parties ect. We also have large event tents, tables, sound systems, lights, and live bands.  Between the kids, the BnB and the Muzik Factory, it is a busy fun life in paradise! We look forward to meeting you!\n\nOur property has a solar heated salt water pool and jacuzzi. Located right in the middle of the island, so you can go to north shore or south shore in 45 min to an hour either way.  No need to bounce accommodations to see the whole island! We live on the property and are available for assistance most of the time. Some of the best restaurants, beaches and shopping is only minutes away. Hiking trails on the Sleeping Giant are within walking distance. \nDue to COVID changes we have changed our room situation and are working on updating the site, so you may receive a message that the specific room you are looking at is no longer available, but we do have a room with exact same furnishing and view from the pool deck remain the same. We also no longer offer a full continental breakfast. We have coffee machine in the rooms with a bowl of fresh local in season fruit , Granola and yogurt. We had to lower the number of people allowed to share the kitchen  due to COVID Hawaii regulations, but you still have full access to the kitchen 24 hours a day:).\nWe do have boogie boards, beach towels, beach chairs and umbrellas, coolers and yoga mats for guest usage. We are a family run operation and look forward to hosting you:). We also have a line on local car rentals as we know sometimes it is hard to find rentals:)\nMahalo,\nDarcy and Tim',
