    source: 'city scrape',
    name: 'Wailea Sunset Condo - Ocean View',
    description:
      "We recently remodeled our condo. Why stay in a resort hotel like the Four Season or the Grand Wailea when you can save 2/3 of the cost and not have to purchase every meal in a resort hotel? You’ll have a full kitchen and BBQ to save on meal costs!<br /><br /><b>The space</b><br />We recently remodeled our condo with all new upgraded tumbled travertine-style flooring, new window shades and new furniture. Why stay in a resort hotel like the Four Season or the Grand Wailea when you can save 2/3 of the cost and not have to purchase every meal in a resort hotel? You’ll have a full kitchen and BBQ to save on meal costs and still enjoy the same view with 3 times the space and privacy. You won't find a better rate in all of Maui for a condo in this location and this size. You won't be disappointed!<br /><br />When you walk into this condo, we know that you will fall in love with it, just like we did! Its bright, airy décor complements the lush green of the gardens of Ekolu and the bright blues",
    neighborhood_overview:
      'Wailea Ekolu Village is the third one built (in 1979) of the four Wailea Villas condo groups.  "Ekolu" means "three" referring to it being the third in that group.  It\'s at 10 Wailea Ekolu Place.<br /><br />Wailea Ekolu condos are up on a hill near the Wailea Tennis Club and the Wailea Blue golf course (now called the Old Blue Course).  It takes about 5 to 10 minutes to walk downhill to Ulua Beach, and longer to walk back uphill, but the drive is only 2 or 3 minutes.  It is less expensive to stay here uphill, than at similar Wailea condos that are on a beach.<br /><br />Location, location, need I say more. We have the most spectacular view of the ocean. During whale watching season you can actual see whales breaching and playing in the channel. With our daily morning walks along the 2 mile path and the close proximity to some of the Maui\'s finest restaurants who would want to be anywhere else.',
    picture_url: 'https://a0.muscache.com/pictures/10926603/c7a53ed9_original.jpg',
    host_id: '4114262',
    host_url: 'https://www.airbnb.com/users/show/4114262',
    host_name: 'Tim & Carolyn',
    host_since: '2012-11-10',
    host_location: 'Aliso Viejo, CA',
    host_about:
      "Carolyn and I live a very busy life in Southern California which a lot of people would consider paradise or at least a very nice place to live. We absolutely LOVE going to Maui a few times a year just to re-charge and enjoy our Maui sunsets.\r\n\r\nWe got Maui'd in November 2002 and ever since we have been bitten by the Aloha Spirit of Maui and especially Wailea. It is by far the best part of Maui with it's manicured lawns and golf courses and great walking path in front of all the resort hotels.\r\n\r\nThe Unique Benefits at this Condo\r\nLocation, location, location....need I say more. We have the most spectacular view of the ocean. During whale watching season you can actual see whales breaching and playing in the channel. With our daily morning walks along the 2 mile path and the close proximity to some of the Maui's finest restaurants who would want to be anywhere else.\r\n\r\nWailea Ekolu Village is a privately owned 148 unit residential-resort condominium Association. Ekolu residents enjoy exclusive use of our two swimming pools, barbeque grills and a recreation pavilion with a complete kitchen and covered open-air lounge. Information on island activities and a stocked library for your reading pleasure is available in the Pavilion adjacent to the Office. Our manager resides on the property and his office and services are the exclusive benefit of the owners and their guest.",
