    name: 'Makai Hideaway',
    description:
      "STVR 19-359915  NUC 19-1121 internet 5 mbps <br /> Cheryl's Makai Hideaway  on youtube.<br />Extra charges around holidays and for larger groups / longer stays. The   new 3 per cent county tax will be added to booking.. There is a rental agreement to be signed on arrival. Swimming by house no beach.<br /> House is not wheelchair accessible.<br /><br /><br /><br /><br /><br /><br /><br />My house  is located one hour ten minutes south of Kona airport . It has fantastic ocean views and deck. 14 minutes off highway 11 on  a curvy road , paved road.<br /><br /><b>The space</b><br />A hidden treasure or hideaway quiet and peaceful, set in an authentic fishing village. Unwind and relax from city life and packaged vacations. Three bedrooms, three baths.. A large Lanai runs the length of the house for dolphin and whale watching. Have dinner on a beautiful Lanai and watch the incredible sunsets . Enjoy the warm climate with cool coastal breezes. One and half blocks to water access for swimming,",
    neighborhood_overview:
      'Quiet hours are 9pm to 8 am per the new county ordinance. I love walking the streets to get exercise. Cars are traveling slowly in the neighbourhood. Speed limit is 15 miles per hour. The swimming hole is two minutes away- go northwest on Kai street to find- its over by the bay. . There are tide pools in the village and the wharf is a great snorkeling place as is the swimming hole by the house. There really isnt a beach close to the house but drive  into the village of Milolii  , the last hawaiian fishing village,park, and hike  15 minutes into Honomolino Beach, a pretty, quiet, black sand beach with a patch of white on the southern part.',
