      "Sunset Suzys Tropical Retreat is located perfectly 15 minutes outside of Hilo town in a beautiful neighborhood called Hawaiian Paradise park.. It is located on a beautiful one acre lot, filled with fruit trees, green grass, green geckos, and tropical birds. There are no neighbors..Only trees, birds and the quiet hush of the wind and very secluded and private<br />Its beautiful cottage located on the Big Island of Hawaii with over 800sq ft is beautifully furnished home.<br /><br /><b>The space</b><br />Located close to Hilo town and airport , just 15 minutes away.<br />A comfy cottage in a jungle type setting. <br />Its a special quiet place located near the volcano, and ocean.  I'm also a quick 30 minute drive from the Hawaii Volcano national park. Im also a 5 minute drive to the most beautiful trails along the ocean.  At the end of my road is Shipmans trailhead that will take you down an ancient Hawaiian path where Hawaiian used to live. <br />You can see their original stone walls st",
    neighborhood_overview:
      "My home is a secluded private sanctuary perfect for enjoying Hawaii's true sense of peace and aloha. The song of birds fill the air and my landscaped acre yard is a great place to take off your shoes and relax!  The ocean is just a quick 5 minute drive and has spectacular sea scales to explore!",
