    host_since: '2013-04-25',
    host_location: 'Hawaii, United States',
    host_about:
      'I am a surfer girl.. who has traveled the world..  Most recently, a few islands in Tahiti.  Had some local boys boat me out to a private island for a day of incredible surf. \r\nI love life, people and nature...... and enjoy the beach and surfing everyday...  Thanking God for the life he has privided me.\r\nI am also a surf instructor and own a surf school.  This property I bought 10 years ago and build this house all on my own with my savings from my surf school.  If your ever on the north shore of Oahu before you go to the big island come surf with me and my dogs. My surf school is called sunset suzys surf school. My website is (Website hidden by Airbnb) \r\n\r\nThank you for viewing and considering my property.  Email if I can help answer any questions about vacationing in Hawaii.  Have a wonderful vacation.  God bless and Aloha!',
