    source: 'city scrape',
    name: 'Beach Condo Ocean Views & Walk everywhere!',
    description:
      '🏄🏽‍♀️ ☀️ Amazing Ocean View Designer Condo - Full kitchen, Remodeled, Four Seasons King bed, Full Ocean Views, huge balcony! Non-smoking building 2022.   🐳 <br /><br />📧 Discounts available - message us first. <br /><br />🏖 Beach, park out front! Walk to everything (no car needed!) – food, drinks, surf, sup, dive, beach, snorkeling, skating, playground, volleyball, tennis, basketball and more! Views of the islands, whales, dolphins.  Fastest internet speed. <br /><br />*Please note: addl Hawaii state tax 17.4%, due upon booking<br /><br /><b>The space</b><br />We are happy to offer my ocean view home to you and your family/friends. 🏝 You have the whole place to yourself. The beach condo is completely private.  🏖 🏄🏽‍♀️ <br /><br />🏡 The place is approximately 900 sq ft and 200 sq ft lanai (balcony).  💕 Luxury King size bed purchased from the Four Seasons. 🌱 Handmade Koa wood luxury table. Hand etched designer glass doors. 🌟 The newly remodeled bathroom features double sinks. Extra large',
    neighborhood_overview:
      "☀️ 👛 The first floor of the building has several restaurants and shops, including 🐠 dive shop (snorkel and beach gear rentals), 🚲 bike shop, 🏬 corner store (snacks, drinks, beach chairs, etc), 💕 massage place, 💅 nail salon, 🍲 healthy breakfast/lunch restaurant with smoothies and sandwiches, local deli with hot plates of yummy food, shops with local 💝 Maui gifts, office store with mail services and fax/computer/printing, real estate office and plenty more. <br /><br />🎉 There are plenty of awesome restaurants in the Kihei-Wailea area (Gannons Wailea, Mala, Duos and Spagos at the Four Seasons, Nick's Fish Market, Sushi. <br /><br />🚗 The condo is in walking distance to everything you need, so you don't even need a car if you prefer to stay in the area. You can walk to the following: Foodland (supermarket with everything you need), an awesome 🍍 farmer's market, 💪 a gym, 🧘‍♂️ yoga studio. Bars, 🎶 live music, and 💃🏻 dancing are also within two minutes walking distance. There is also a skate",
    picture_url: 'https://a0.muscache.com/pictures/4869b5a6-10d1-4999-849b-ce089aa0dcde.jpg',
    host_id: '4122306',
    host_url: 'https://www.airbnb.com/users/show/4122306',
    host_name: 'Monica',
    host_since: '2012-11-11',
    host_location: 'Kihei, HI',
    host_about:
      "Aloha,\r\nI have been living in Maui for years and love it here.  I do plenty of activities on the island so I keep super busy.  I dance hula with a halau and we perform every so often.  You can stand up paddle right in front of the place every morning before work and see little turtle friends, sometimes rays and dolphins and right now lots of whales.  It's beautiful out here.  You can go to yoga every evening which is just a short walk away.  I also love swimming laps at our local community pool, which is free to the public.  Beautiful facility right down the street. it's a great life when you can come home to the ocean and sunshine everyday. \r\n\r\nI love to travel and see the world.  I have been to Peru, Chile, Kenya, Philippines and soon hopefully Nepal. I have also visited Austria, Scotland and soon Australia and England.  Looking forward to it all, of course.  \r\n\r\nABOUT THE PLACE:  \r\nIt is an AWESOME location!! You can walk to EVERYTHING! \r\n-Beach is across the street\r\n-Surf, swim, stand up paddle right across the street. Also roller skating rink, volleyball, basketball, tennis courts, soccer and more across the street! \r\n-Restaurants, bars, shops, rentals (bike, scuba, snorkel, surf) all downstairs\r\n-Tours around Maui or outer islands (bike, boat, snorkel, dive, helicopter, anything) are all downstairs.  \r\n-Yoga classes, gym 5-10 minute walk\r\n-Supermarket is a 5 minute walk. \r\n-Airport is 20 minute drive\r\n-Car rental (Kihei Rent a Car, Avis, etc) is a few minutes away.\r\n-We are located right next to Kalama Park\r\nOnce your reservation is confirmed, you can see the house manual that has all the awesome places and restaurants nearby listed :) \r\n\r\nThis is the best location on Maui for the combination of awesome day life and night life.  You don't even need a car :) \r\n\r\nI look forward to hearing from all of you soon. \r\n\r\nWarm aloha. ",
